import { BigNumber } from 'ethers';

export enum TnxStatus {
  FAILED = 0,
  SUCCESS = 1,
  UNKNOWN = 2,
}

export enum ConfirmStatus {
  ERROR = 0,
  WARNING = 1,
  INFO = 2,
}

export interface ICurrencyInput {
  symbol: string;
  name: string;
  address: string;
  decimals: number;
  logoURI: string;
  burn: number;
  balance: string;
  price: number;
  liquidity: number;
  quoteSymbol: string;
  value: string;
  allowance: string;
}

export interface IRouteSwap {
  symbol: string;
  address: string;
}

export interface IPriceImpact {
  priceImpact: number;
  amountOut?: string;
}

export interface ITokenImpact {
  address: string;
  price: number;
}

export interface ITnxConfig {
  proxy: string;
  tnxGas: number;
  gasPrice: number;
  slip: number;
  fee: { isActive: boolean; fee: number };
}

export interface IDAppConfig {
  factory: string; // factory AMM
  hashCode: string; // hashCode AMM
}

export interface ITnxReciept {
  status?: TnxStatus;
  tnxHash?: string;
  errorCode?: string;
}

export interface IProxySwapParam extends IDAppConfig {
  amountIn?: BigNumber; // option for send BNB
  amountOutMin: BigNumber;
  path: string[]; // route swap
  to: string; // wallet address
}

export interface ITnxParam {
  from: string; // wallet address
  gasLimit: BigNumber; // gas limit for tnx
  gasPrice: BigNumber; //  price per gas unit (wei)
  nonce: number; // tnx nonce
  value?: BigNumber; // option for send BNB
}
