import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../../config/tokenBep20Config';
import tokenHelper from '../../../helper/tokenHelper';
import swapHelper from '../../../helper/swapHelper';
import { ITokenImpact } from '../../../model/swapModel';
import style from './PriceImpact.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isLoading: boolean;
  priceImpact: number;
  tokenInput: ITokenImpact;
  tokenOutput: ITokenImpact;
}
interface IOwnState {}

class PriceImpact extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isLoading: false,
  };

  state: IOwnState;

  private quoteAddress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
    BNB_BEP20.address,
  ];

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  private buildPriceWithImpact(): string {
    const { isLoading, priceImpact, tokenInput, tokenOutput } = this.props;

    if (isLoading) {
      return 'Loading...';
    }

    if (priceImpact === -1) {
      return 'UNKNOW';
    }

    const impactRound = _.round(priceImpact * 100, 2) + '%';

    if (tokenInput.address === tokenOutput.address) {
      return '0%';
    }

    if (
      (this.quoteAddress.includes(tokenInput.address) &&
        this.quoteAddress.includes(tokenOutput.address)) ||
      (!this.quoteAddress.includes(tokenInput.address) &&
        !this.quoteAddress.includes(tokenOutput.address))
    ) {
      return impactRound;
    }

    if (!this.quoteAddress.includes(tokenInput.address) && !!tokenInput.price) {
      const priceRound: string = tokenHelper.roundCurrency(
        tokenInput.price * (1 - priceImpact)
      );

      return `($${priceRound}) ${impactRound}`;
    }

    if (
      !this.quoteAddress.includes(tokenOutput.address) &&
      !!tokenInput.price
    ) {
      const priceRound: string = tokenHelper.roundCurrency(
        tokenOutput.price * (1 + priceImpact)
      );

      return `($${priceRound}) ${impactRound}`;
    }

    return '0%';
  }

  render(): ReactElement {
    const { priceImpact } = this.props;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Price impact</div>
        <div
          className={swapHelper.getClassWarningPriceImpact(priceImpact * 100)}
        >
          {this.buildPriceWithImpact()}
        </div>
      </div>
    );
  }
}

export default PriceImpact;
