import _ from 'lodash';
import { toast } from 'react-toastify';
import {
  utils,
  Contract,
  Wallet,
  BigNumber,
  ContractTransaction,
  ContractReceipt,
} from 'ethers';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../config/tokenBep20Config';
import { MAX_UINT256 } from '../config/appSwapConfig';
import swapHelper from '../helper/swapHelper';
import { DApp, ITokenDetail } from '../model/tokenModel';
import {
  ICurrencyInput,
  IRouteSwap,
  IPriceImpact,
  ITnxConfig,
  IDAppConfig,
  TnxStatus,
  ITnxReciept,
  IProxySwapParam,
  ITnxParam,
} from '../model/swapModel';
import {
  bscProvider,
  getBep20ProxyContractSigner,
  getBep20Contract,
  getBep20ContractSigner,
} from './web3Service';
import { TPoolAddressReserve, getTokenPoolReserve, mapTokenPoolFromReserve, getTokenPool } from '../service/tokenPoolService';
import { getTokenPrice } from '../service/tokenPriceService';

const stableCoinAdress = [
  BUSD_BEP20.address,
  USDT_BEP20.address,
  USDC_BEP20.address,
];
const rootAddress: string[] = [...stableCoinAdress, BNB_BEP20.address];

export async function getTokenPoolReserveSwaps(
  tokens: [ITokenDetail, ITokenDetail]
): Promise<TPoolAddressReserve> {
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let poolAddressReserve: TPoolAddressReserve = [[], [], [], []];

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    poolAddressReserve = await getTokenPoolReserve(requestTokens);
  }

  return poolAddressReserve;
}

export function mapTokenPoolFromReserveSwaps(
  tokens: [ITokenDetail, ITokenDetail],
  poolAddressReserve: TPoolAddressReserve,
): [ITokenDetail, ITokenDetail]{
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let tokenSwaps: [ITokenDetail, ITokenDetail] = tokens;

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    requestTokens = mapTokenPoolFromReserve(requestTokens, poolAddressReserve);
  }

  tokens.forEach((token: ITokenDetail, i: number) => {
    tokenSwaps[i] = (_.find(nonRequestTokens, { address: token.address }) ||
      _.find(requestTokens, { address: token.address })) as ITokenDetail;
  });

  return tokenSwaps;
}

export async function getTokenPoolSwaps(
  tokens: [ITokenDetail, ITokenDetail]
): Promise<[ITokenDetail, ITokenDetail]> {
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let tokenSwaps: [ITokenDetail, ITokenDetail] = tokens;

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    requestTokens = await getTokenPool(requestTokens);
  }

  tokens.forEach((token: ITokenDetail, i: number) => {
    tokenSwaps[i] = (_.find(nonRequestTokens, { address: token.address }) ||
      _.find(requestTokens, { address: token.address })) as ITokenDetail;
  });

  return tokenSwaps;
}

export async function getTokenSwaps(
  tokens: [ITokenDetail, ITokenDetail],
  dApp: DApp
): Promise<[ITokenDetail, ITokenDetail]> {
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let tokenSwaps: [ITokenDetail, ITokenDetail] = tokens;

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    } else if (_.isNil(token.targetPool)) {
      nonRequestTokens.push(token);
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    requestTokens = await getTokenPrice(requestTokens, dApp);
  }

  tokens.forEach((token: ITokenDetail, i: number) => {
    tokenSwaps[i] = (_.find(nonRequestTokens, { address: token.address }) ||
      _.find(requestTokens, { address: token.address })) as ITokenDetail;
  });

  return tokenSwaps;
}

export function getRouteSwap(
  tokenInput: ITokenDetail,
  tokenOutput: ITokenDetail,
  dApp: DApp
): IRouteSwap[] {
  // Pancake: BUSD/USDT, BUSD/USDC, BUSD/BNB, USDT/USDC, USDT/BNB
  // Ape: BUSD/USDT, BUSD/USDC, BUSD/BNB

  if (tokenInput.address === tokenOutput.address) {
    return [];
  }

  const routeInput: IRouteSwap = {
    symbol: tokenInput.symbol as string,
    address: tokenInput.address,
  };
  const routeInputPair: IRouteSwap = {
    symbol: tokenInput.targetPool?.quoteSymbol as string,
    address: tokenInput.targetPool?.quoteAddress as string,
  };
  const routeOutput: IRouteSwap = {
    symbol: tokenOutput.symbol as string,
    address: tokenOutput.address,
  };
  const routeOutputPair: IRouteSwap = {
    symbol: tokenOutput.targetPool?.quoteSymbol as string,
    address: tokenOutput.targetPool?.quoteAddress as string,
  };
  let routeSwaps: IRouteSwap[] = [routeInput];
  let routePrev: IRouteSwap;

  if (!rootAddress.includes(routeInput.address)) {
    routeSwaps.push(routeInputPair);
  }

  switch (dApp) {
    case DApp.PANCAKE:
      const unBridge: [string, string] = [
        USDC_BEP20.address,
        BNB_BEP20.address,
      ];

      routePrev = _.last(routeSwaps) as IRouteSwap;

      if (rootAddress.includes(routeOutput.address)) {
        if (routeOutput.address !== routePrev.address) {
          if (
            unBridge.includes(routeOutput.address) &&
            unBridge.includes(routePrev.address)
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutput);
        }
      } else {
        if (routeOutputPair.address !== routePrev.address) {
          if (
            unBridge.includes(routeOutputPair.address) &&
            unBridge.includes(routePrev.address)
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutputPair);
        }

        routeSwaps.push(routeOutput);
      }

      break;

    case DApp.APE:
      routePrev = _.last(routeSwaps) as IRouteSwap;

      if (rootAddress.includes(routeOutput.address)) {
        if (routeOutput.address !== routePrev.address) {
          if (
            routeOutput.address !== BUSD_BEP20.address &&
            routePrev.address !== BUSD_BEP20.address
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }

          routeSwaps.push(routeOutput);
        }
      } else {
        if (routeOutputPair.address !== routePrev.address) {
          if (
            routeOutputPair.address !== BUSD_BEP20.address &&
            routePrev.address !== BUSD_BEP20.address
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutputPair);
        }

        routeSwaps.push(routeOutput);
      }

      break;
  }

  return routeSwaps;
}

export function getPriceImpact(
  amountTraded: number,
  tokenInput: ITokenDetail,
  tokenOutput: ITokenDetail,
  routeSwaps: IRouteSwap[],
  bnbPrice: number
): IPriceImpact {
  if (!swapHelper.validRouteSwaps(routeSwaps)) {
    return {
      priceImpact: -1,
    };
  }

  if (!amountTraded || routeSwaps.length === 0) {
    return {
      priceImpact: 0,
    };
  }

  const fee = 0.0025;
  const routes: IRouteSwap[] = routeSwaps.map(
    (route: IRouteSwap): IRouteSwap => ({
      ...route,
      address: route.address,
    })
  );
  const amountForFee: number = amountTraded * (routes.length - 1) * fee;
  let routeTraded = {
    address: tokenInput.address,
    amount: amountTraded,
    price: tokenInput.price as number,
  };
  let priceImpact: number;

  routes.forEach((route: IRouteSwap, index: number) => {
    if (index === routes.length - 1) {
      return;
    }

    if (
      rootAddress.includes(route.address) &&
      rootAddress.includes(routes[index + 1].address)
    ) {
      let nextPrice: number = 1;
      let nextAmount: number = routeTraded.amount * (1 - fee);

      if (route.address === BNB_BEP20.address) {
        nextAmount = nextAmount * bnbPrice;
      }

      if (routes[index + 1].address === BNB_BEP20.address) {
        nextPrice = bnbPrice;
        nextAmount = nextAmount / nextPrice;
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }

    if (rootAddress.includes(route.address)) {
      const nextPrice: number = tokenOutput.price as number;
      let nextAmount: number = routeTraded.amount * (1 - fee);
      const reserve: number = Number(tokenOutput.targetPool?.reserve?.[0] || 0);
      const impact: number = nextAmount / (reserve + nextAmount);

      nextAmount = nextAmount * (1 - impact);

      if (route.address === BNB_BEP20.address) {
        nextAmount = (nextAmount * bnbPrice) / nextPrice;
      } else {
        nextAmount = nextAmount / nextPrice;
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }

    if (rootAddress.includes(routes[index + 1].address)) {
      let nextPrice: number = 1;
      let nextAmount: number = routeTraded.amount * (1 - fee);
      const reserve: number = Number(tokenInput.targetPool?.reserve?.[1] || 0);
      const impact: number = nextAmount / (reserve + nextAmount);

      nextAmount = nextAmount * (1 - impact);

      if (routes[index + 1].address === BNB_BEP20.address) {
        nextPrice = bnbPrice;
        nextAmount = (nextAmount * (tokenInput.price as number)) / nextPrice;
      } else {
        nextAmount = nextAmount * (tokenInput.price as number);
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }
  });

  priceImpact =
    (amountTraded -
      amountForFee -
      (routeTraded.amount * (tokenOutput.price as number)) /
        (tokenInput.price as number)) /
    amountTraded;

  if (_.round(priceImpact, 2) === 0) {
    priceImpact = fee * (routeSwaps.length - 1);
  }

  return {
    priceImpact: Math.abs(priceImpact),
    amountOut: _.trimEnd(routeTraded.amount.toFixed(tokenOutput.decimals), '.0'),
  };
}

export async function getTokenBalanceOfWallet(
  walletAddress: string,
  tokenAddress: string,
  tokenDecimals: number = 18
): Promise<string> {
  let balanceBN: BigNumber = BigNumber.from(0);

  if (tokenAddress === BNB_BEP20.address) {
    try {
      balanceBN = await bscProvider.getBalance(walletAddress);
    } catch (error) {
      toast.error('Get balance failed!');
      console.log(error);
    }

    const balance: string = utils.formatUnits(balanceBN, tokenDecimals);

    return balance;
  }

  const tokenContract: Contract = getBep20Contract(tokenAddress);

  try {
    balanceBN = await tokenContract.balanceOf(walletAddress);
  } catch (error) {
    toast.error('Get balance failed!');
    console.log(error);
  }

  const balance: string = utils.formatUnits(balanceBN, tokenDecimals);

  return balance;
}

export async function getTokenAllowance(
  walletAddress: string,
  tokenAddress: string,
  proxy: string,
  tokenDecimals: number = 18
): Promise<string> {
  let allowanceBN: BigNumber = BigNumber.from(0);

  if (!walletAddress) {
    return '0';
  }

  if (tokenAddress === BNB_BEP20.address) {
    return BigNumber.from(MAX_UINT256).toString();
  }

  const tokenContract: Contract = getBep20Contract(tokenAddress);

  try {
    allowanceBN = await tokenContract.allowance(walletAddress, proxy);
  } catch (error) {
    toast.error('Get allowance error!');
    console.log('Allowance error: ', error);
  }

  return utils.formatUnits(allowanceBN, tokenDecimals);
}

export async function approveToken(
  tokenAddress: string,
  wallet: Wallet,
  tnxConfig: ITnxConfig,
  maxApproveSafe?: BigNumber
): Promise<ITnxReciept> {
  if (tokenAddress === BNB_BEP20.address) {
    return { status: TnxStatus.SUCCESS };
  }

  const tokenContractSigner: Contract = getBep20ContractSigner(
    tokenAddress,
    wallet
  );
  let status: TnxStatus = TnxStatus.UNKNOWN;
  let receipt: ContractReceipt = {} as ContractReceipt;
  let errorCode: string = '';

  try {
    const approveTnx: ContractTransaction = await tokenContractSigner.approve(
      tnxConfig.proxy,
      maxApproveSafe ? maxApproveSafe : BigNumber.from(MAX_UINT256),
      maxApproveSafe
        ? {
            gasLimit: BigNumber.from(_.round(tnxConfig.tnxGas)),
            gasPrice: utils.parseUnits(
              _.round(tnxConfig.gasPrice).toString(),
              'gwei'
            ),
          }
        : {}
    );
    receipt = await approveTnx.wait();
  } catch (error: any) {
    receipt = error.receipt || {};
    errorCode = error.code;
    console.log('Approve: ', error);
  }

  switch (receipt.status) {
    case TnxStatus.SUCCESS:
      status = TnxStatus.SUCCESS;
      break;

    case TnxStatus.FAILED:
      status = TnxStatus.FAILED;
      console.log(
        `Approve failed${errorCode ? ' (' + errorCode + ')' : ''}: `,
        receipt
      );
      break;

    default:
      status = TnxStatus.UNKNOWN;
      console.log(
        `Approve unknown${errorCode ? ' (' + errorCode + ')' : ''}: `,
        receipt
      );
      break;
  }

  return {
    status,
    tnxHash: receipt.transactionHash,
    errorCode,
  };
}

export async function swapTokens(
  wallet: Wallet,
  dApp: DApp,
  routeSwaps: IRouteSwap[],
  tokenInput: ICurrencyInput,
  tokenOutput: ICurrencyInput,
  tnxConfig: ITnxConfig
): Promise<ITnxReciept> {
  let nonce: number;

  try {
    nonce = await bscProvider.getTransactionCount(wallet.address, 'latest');
  } catch (error) {
    toast.error('Get nonce failed!');
    console.log('Get nonce: ', error);

    return {};
  }

  const proxyContractSigner: Contract = getBep20ProxyContractSigner(
    wallet,
    tnxConfig.proxy
  );
  const dAppConfig: IDAppConfig = swapHelper.getDappConfig(dApp);
  const proxySwapParam: IProxySwapParam = {
    ...dAppConfig,
    amountIn: swapHelper.parseUnitWithDecimals(
      tokenInput.value,
      tokenInput.decimals
    ),
    amountOutMin: swapHelper.parseUnitWithDecimals(
      tokenOutput.value,
      tokenOutput.decimals,
      tnxConfig.slip + (tnxConfig.fee.isActive ? tnxConfig.fee.fee : 0)
    ),
    path: _.map(routeSwaps, 'address'),
    to: wallet.address,
  };
  let tnxParam: ITnxParam = {
    from: wallet.address,
    gasLimit: BigNumber.from(_.round(tnxConfig.tnxGas)),
    gasPrice: utils.parseUnits(_.round(tnxConfig.gasPrice).toString(), 'gwei'),
    nonce,
  };
  let status: TnxStatus = TnxStatus.UNKNOWN;
  let swapTnxPromise: Promise<ContractTransaction> =
    {} as Promise<ContractTransaction>;
  let swapTnx: ContractTransaction = {} as ContractTransaction;
  let receipt: ContractReceipt = {} as ContractReceipt;
  let errorCode: string = '';

  if (![tokenInput.address, tokenOutput.address].includes(BNB_BEP20.address)) {
    if (tnxConfig.fee.isActive) {
      // swapExactTokensForTokensSupportingFeeOnTransferTokens
      swapTnxPromise =
        proxyContractSigner.swapExactTokensForTokensSupportingFeeOnTransferTokens(
          proxySwapParam.factory,
          proxySwapParam.hashCode,
          proxySwapParam.amountIn,
          proxySwapParam.amountOutMin,
          proxySwapParam.path,
          proxySwapParam.to,
          tnxParam
        );
    } else {
      // swapExactTokensForTokens
      swapTnxPromise = proxyContractSigner.swapExactTokensForTokens(
        proxySwapParam.factory,
        proxySwapParam.hashCode,
        proxySwapParam.amountIn?.toString(),
        proxySwapParam.amountOutMin.toString(),
        proxySwapParam.path,
        proxySwapParam.to,
        tnxParam
      );
    }
  } else if (
    tokenInput.address === BNB_BEP20.address &&
    tokenOutput.address !== BNB_BEP20.address
  ) {
    tnxParam.value = proxySwapParam.amountIn;

    if (tnxConfig.fee.isActive) {
      // swapExactBNBForTokensSupportingFeeOnTransferTokens
      swapTnxPromise =
        proxyContractSigner.swapExactBNBForTokensSupportingFeeOnTransferTokens(
          proxySwapParam.factory,
          proxySwapParam.hashCode,
          proxySwapParam.amountOutMin,
          proxySwapParam.path,
          proxySwapParam.to,
          tnxParam
        );
    } else {
      // swapExactBNBForTokens
      swapTnxPromise = proxyContractSigner.swapExactBNBForTokens(
        proxySwapParam.factory,
        proxySwapParam.hashCode,
        proxySwapParam.amountOutMin,
        proxySwapParam.path,
        proxySwapParam.to,
        tnxParam
      );
    }
  } else if (
    tokenInput.address !== BNB_BEP20.address &&
    tokenOutput.address === BNB_BEP20.address
  ) {
    if (tnxConfig.fee.isActive) {
      // swapExactTokensForBNBSupportingFeeOnTransferTokens
      swapTnxPromise =
        proxyContractSigner.swapExactTokensForBNBSupportingFeeOnTransferTokens(
          proxySwapParam.factory,
          proxySwapParam.hashCode,
          proxySwapParam.amountIn,
          proxySwapParam.amountOutMin,
          proxySwapParam.path,
          proxySwapParam.to,
          tnxParam
        );
    } else {
      // swapExactTokensForBNB
      swapTnxPromise = proxyContractSigner.swapExactTokensForBNB(
        proxySwapParam.factory,
        proxySwapParam.hashCode,
        proxySwapParam.amountIn,
        proxySwapParam.amountOutMin,
        proxySwapParam.path,
        proxySwapParam.to,
        tnxParam
      );
    }
  } else {
    return { status: TnxStatus.UNKNOWN };
  }

  try {
    swapTnx = await swapTnxPromise;
    toast.info('Waiting confirm...');
    receipt = await swapTnx.wait();
  } catch (error: any) {
    receipt = error.receipt || {};
    errorCode = error.code;
    console.log('Swap: ', error);
  }

  switch (receipt.status) {
    case TnxStatus.SUCCESS:
      status = TnxStatus.SUCCESS;
      break;

    case TnxStatus.FAILED:
      status = TnxStatus.FAILED;
      console.log(
        `Swap failed${errorCode ? ' (' + errorCode + ')' : ''}: `,
        receipt
      );
      break;

    default:
      status = TnxStatus.UNKNOWN;
      console.log(
        `Swap unknown${errorCode ? ' (' + errorCode + ')' : ''}: `,
        receipt
      );
      break;
  }

  return {
    status,
    tnxHash: receipt.transactionHash,
    errorCode,
  };
}

const swapService = {
  getTokenPoolReserveSwaps,
  mapTokenPoolFromReserveSwaps,
  getTokenPoolSwaps,
  getTokenSwaps,
  getRouteSwap,
  getPriceImpact,
  getTokenBalanceOfWallet,
  getTokenAllowance,
  approveToken,
  swapTokens,
};

export default swapService;
