import _ from 'lodash';

function getClassPrice(percent: number): string {
  if (percent > 0) {
    return 'fk-text-green';
  } else if (percent === 0) {
    return '';
  } else {
    return 'fk-text-red';
  }
}

// supplyDetal = newSupply - oldSupply
function getClassSupply(supplyDetal: number): string {
  if (supplyDetal <= 0) {
    return '';
  } else {
    return 'fk-text-red';
  }
}

function getClassTax(tax: number = -1): string {
  if (tax >= 8) {
    return 'fk-text-red';
  } else if (tax > 0 && tax < 8) {
    return 'fk-text-orange';
  } else if (tax === -1) {
    return '';
  } else {
    return 'fk-text-green';
  }
}

function getClassYesRisk(v?: boolean): string {
  if (_.isNil(v)) {
    return '';
  } else if (v) {
    return 'fk-text-red';
  } else {
    return 'fk-text-green';
  }
}

function getClassYesWarning(v?: boolean): string {
  if (_.isNil(v)) {
    return '';
  } else if (v) {
    return 'fk-text-orange';
  } else {
    return 'fk-text-green';
  }
}

function getClassYesTrust(v?: boolean): string {
  if (_.isNil(v)) {
    return '';
  } else if (v) {
    return 'fk-text-green';
  } else {
    return 'fk-text-red';
  }
}

const helper = {
  getClassPrice,
  getClassSupply,
  getClassTax,
  getClassYesRisk,
  getClassYesWarning,
  getClassYesTrust,
};

export default helper;
