import { PureComponent, ReactElement, Fragment } from 'react';
import { Modal } from 'reactstrap';
import { ConfirmStatus } from '../../../model/swapModel';
import ButtonBase from '../../components/ButtonBase/ButtonBase';
import style from './ModalConfirm.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isOpen: boolean;
  status: ConfirmStatus;
  title: string;
  contents: string[];
  onToggle: () => void;
  onConfirm: () => void;
  onCancel?: () => void;
}
interface IOwnState {}

class ModalConfirm extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isOpen: false,
    status: ConfirmStatus.INFO,
    title: 'Confirm',
    content: ['Are you sure?'],
    onToggle: () => {},
    onConfirm: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onToggle = this.onToggle.bind(this);
    this.onConfirm = this.onConfirm.bind(this);
    this.onCancel = this.onCancel.bind(this);
  }

  private onToggle() {
    this.props.onToggle();
  }

  private onConfirm() {
    this.props.onConfirm();
  }

  private onCancel() {
    const { onCancel } = this.props;

    onCancel && onCancel();
  }

  render(): ReactElement {
    const { isOpen, status, title, contents, onCancel } = this.props;

    return (
      <Modal
        centered
        className='fk-modal'
        isOpen={isOpen}
        fade={false}
        animation='false'
        toggle={this.onToggle}
      >
        <div className='d-flex justify-content-between p-4 header'>
          <span
            className={
              'header-title' +
              (status === ConfirmStatus.WARNING ? ' fk-swap-text-error' : '')
            }
          >
            {title}
          </span>
          <button className='fk-button-icon' onClick={this.onToggle}>
            <i className='bi bi-x-lg'></i>
          </button>
        </div>
        <div className='p-4 body'>
          <div className='my-3 content-text'>
            {contents.map(
              (content: string): ReactElement => (
                <p>{content}</p>
              )
            )}
          </div>
        </div>
        <div className='d-flex justify-content-center p-4 footer'>
          <ButtonBase
            className={'fk-button-primary ' + style.btn}
            title='Yes'
            onClick={this.onConfirm}
          />
          {onCancel && (
            <Fragment>
              <div className='mx-2'></div>
              <ButtonBase
                className={'fk-button-secondary ' + style.btn}
                title='Cancel'
                onClick={this.onCancel}
              />
            </Fragment>
          )}
        </div>
      </Modal>
    );
  }
}

export default ModalConfirm;
