import logoBep20 from '../../../asset/logo/bep20.png';
import style from './ToastLink.module.scss';

const ToastLink = ({
  message,
  link,
  logoURI,
}: {
  message: string;
  link?: string;
  logoURI?: string;
}) => (
  <div>
    <img className={style.logo} alt={'NA'} src={logoURI || logoBep20} />
    {link ? (
      <a
        className={style.titleLink}
        href={link}
        target='_blank'
        rel='noopener noreferrer'
      >
        {message}
      </a>
    ) : (
      <span className={style.title}>{message}</span>
    )}
  </div>
);

export default ToastLink;
