import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { TXN_GAS_PRICE_MAX, TXN_SLIP_MAX } from '../../../config/appSwapConfig';
import { TStoreState } from '../../../store/config/store';
import { TTxnPriority } from '../../../store/swapSettingReducer';
import Checkbox from '../controls/Checkbox/Checkbox';
import Input from '../controls/InputInt/InputInt';
import style from './GasSlip.module.scss';

interface IStoreProps {
  txnPriorityLow: TTxnPriority;
  txnPriorityMedium: TTxnPriority;
  txnPriorityHight: TTxnPriority;
}
interface IHOCProps {}
interface IOwnProps {
  onChange: (v: { gasPrice: number; slip: number }) => void;
}
interface IOwnState {
  isLowCheck: boolean;
  isMediumCheck: boolean;
  isHightCheck: boolean;
  gasPrice: number;
  slip: number;
}

class GasSlip extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    onChange: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isLowCheck: true,
      isMediumCheck: false,
      isHightCheck: false,
      gasPrice: props.txnPriorityLow.gasPrice,
      slip: props.txnPriorityLow.slip,
    };

    this.raiseGasSlipSetting();

    this.onLowChkChange = this.onLowChkChange.bind(this);
    this.onMediumChkChange = this.onMediumChkChange.bind(this);
    this.onHightChkChange = this.onHightChkChange.bind(this);
    this.onGasPriceInputChange = this.onGasPriceInputChange.bind(this);
    this.onSlipInputChange = this.onSlipInputChange.bind(this);
  }

  componentDidUpdate(prevProps: IStoreProps) {
    const { isLowCheck, isMediumCheck, isHightCheck, gasPrice, slip } =
      this.state;

    if (!_.isEqual(this.props.txnPriorityLow, prevProps.txnPriorityLow)) {
      if (isLowCheck) {
        this.onLowChkChange(isLowCheck);
      } else {
        this.handleGasSlipInput(gasPrice, slip);
      }
    }

    if (!_.isEqual(this.props.txnPriorityMedium, prevProps.txnPriorityMedium)) {
      if (isMediumCheck) {
        this.onLowChkChange(isMediumCheck);
      } else {
        this.handleGasSlipInput(gasPrice, slip);
      }
    }

    if (!_.isEqual(this.props.txnPriorityHight, prevProps.txnPriorityHight)) {
      if (isHightCheck) {
        this.onLowChkChange(isHightCheck);
      } else {
        this.handleGasSlipInput(gasPrice, slip);
      }
    }
  }

  private onLowChkChange(value: boolean) {
    if (value) {
      const { txnPriorityLow } = this.props;

      this.setState(
        {
          gasPrice: txnPriorityLow.gasPrice,
          slip: txnPriorityLow.slip,
        },
        () => {
          this.raiseGasSlipSetting();
        }
      );
      this.setLowChecked();
    }
  }

  private onMediumChkChange(value: boolean) {
    if (value) {
      const { txnPriorityMedium } = this.props;

      this.setState(
        {
          gasPrice: txnPriorityMedium.gasPrice,
          slip: txnPriorityMedium.slip,
        },
        () => {
          this.raiseGasSlipSetting();
        }
      );
      this.setMediumChecked();
    }
  }

  private onHightChkChange(value: boolean) {
    if (value) {
      const { txnPriorityHight } = this.props;

      this.setState(
        {
          gasPrice: txnPriorityHight.gasPrice,
          slip: txnPriorityHight.slip,
        },
        () => {
          this.raiseGasSlipSetting();
        }
      );
      this.setHightChecked();
    }
  }

  private onGasPriceInputChange(value: number) {
    this.handleGasSlipInput(value, this.state.slip);
  }

  private onSlipInputChange(value: number) {
    this.handleGasSlipInput(this.state.gasPrice, value);
  }

  private handleGasSlipInput(gasPrice: number, slip: number) {
    const { txnPriorityLow, txnPriorityMedium, txnPriorityHight } = this.props;

    if (txnPriorityLow.gasPrice === gasPrice && txnPriorityLow.slip === slip) {
      this.setLowChecked();
    } else if (
      txnPriorityMedium.gasPrice === gasPrice &&
      txnPriorityMedium.slip === slip
    ) {
      this.setMediumChecked();
    } else if (
      txnPriorityHight.gasPrice === gasPrice &&
      txnPriorityHight.slip === slip
    ) {
      this.setHightChecked();
    } else {
      this.setUncheckAll();
    }

    this.setState({ gasPrice, slip }, () => {
      this.raiseGasSlipSetting();
    });
  }

  private setLowChecked() {
    this.setState({
      isLowCheck: true,
      isMediumCheck: false,
      isHightCheck: false,
    });
  }

  private setMediumChecked() {
    this.setState({
      isLowCheck: false,
      isMediumCheck: true,
      isHightCheck: false,
    });
  }

  private setHightChecked() {
    this.setState({
      isLowCheck: false,
      isMediumCheck: false,
      isHightCheck: true,
    });
  }

  private setUncheckAll() {
    this.setState({
      isLowCheck: false,
      isMediumCheck: false,
      isHightCheck: false,
    });
  }

  private raiseGasSlipSetting() {
    this.props.onChange(_.pick(this.state, ['gasPrice', 'slip']));
  }

  render(): ReactElement {
    const { isLowCheck, isMediumCheck, isHightCheck, gasPrice, slip } =
      this.state;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div className='d-flex justify-content-between align-items-center'>
          <div>
            <span className={style.space}>Gas</span>
            <Input
              classInput='fk-swap-text-info'
              title='Gw'
              value={gasPrice}
              maxValue={TXN_GAS_PRICE_MAX}
              onChange={this.onGasPriceInputChange}
            />
          </div>
          <span className='mx-2'>-</span>
          <div>
            <span className={style.space}>Slip</span>
            <Input
              classInput='fk-swap-text-info'
              title='%'
              value={slip}
              maxValue={TXN_SLIP_MAX}
              onChange={this.onSlipInputChange}
            />
          </div>
        </div>
        <div className='d-flex align-items-center'>
          <div className={style.checkbox}>
            <Checkbox
              leftTitle='L'
              isCheck={isLowCheck}
              onChkChange={this.onLowChkChange}
            />
          </div>
          <div className={style.checkbox}>
            <Checkbox
              leftTitle='M'
              isCheck={isMediumCheck}
              onChkChange={this.onMediumChkChange}
            />
          </div>
          <div className={style.checkbox}>
            <Checkbox
              leftTitle='H'
              isCheck={isHightCheck}
              onChkChange={this.onHightChkChange}
            />
          </div>
        </div>
      </div>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  txnPriorityLow: state.swapSetting.txnPriorityLow,
  txnPriorityMedium: state.swapSetting.txnPriorityMedium,
  txnPriorityHight: state.swapSetting.txnPriorityHight,
});

export default connect(mapState)(GasSlip);
