import { PureComponent, ReactElement } from 'react';
import tokenHelper from '../../../helper/tokenHelper';
import style from './ButtonWalletConnected.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  addressWallet: string;
  onClick: () => void;
}
interface IOwnState {}

class ButtonWalletConnected extends PureComponent<
  IStoreProps & IHOCProps & IOwnProps,
  IOwnState
> {
  static defaultProps = {
    addressWallet: '',
    onClick: () => {},
  };

  state: IOwnState;

  constructor(props: IStoreProps & IOwnProps & IHOCProps) {
    super(props);

    this.state = {};
    this.onClick = this.onClick.bind(this);
  }

  private onClick() {
    this.props.onClick();
  }

  render(): ReactElement {
    const { addressWallet } = this.props;

    return (
      <div
        className={
          'd-flex d-flex justify-content-center align-items-center ' +
          style.wrap
        }
      >
        <span className={style.text}>
          {tokenHelper.getShortAddress(addressWallet)}
        </span>
        <button
          className={'p-0 fk-button-icon ' + style.btn}
          onClick={this.onClick}
        >
          <i className='bi bi-x-lg'></i>
        </button>
      </div>
    );
  }
}

export default ButtonWalletConnected;
