import _ from 'lodash';
import { PureComponent, ReactElement } from 'react';
import { IRouteSwap } from '../../../model/swapModel';
import swapHelper from '../../../helper/swapHelper';
import style from './RouteSwap.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isLoading: boolean;
  routeSwaps: IRouteSwap[];
}
interface IOwnState {}

class RouteSwap extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isLoading: false,
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  private buildRoute() {
    const { isLoading, routeSwaps } = this.props;

    if (isLoading) {
      return 'Loading...';
    }

    if (!swapHelper.validRouteSwaps(routeSwaps)) {
      return `${routeSwaps[0].symbol} > UNKNOW > ${_.last(routeSwaps)?.symbol}`;
    }

    return _.map(routeSwaps, 'symbol').join(' > ');
  }

  render(): ReactElement {
    const { routeSwaps } = this.props;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Route</div>
        <div
          className={
            swapHelper.validRouteSwaps(routeSwaps) ? '' : 'fk-swap-text-error'
          }
        >
          {this.buildRoute()}
        </div>
      </div>
    );
  }
}

export default RouteSwap;
