import { PureComponent, ReactElement } from 'react';
import { TXN_FEE_MAX } from '../../../config/appSwapConfig';
import swapHelper from '../../../helper/swapHelper';
import Checkbox from '../controls/Checkbox/Checkbox';
import Input from '../controls/InputInt/InputInt';
import style from './Fee.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  inputFee: number;
  outputFee: number;
  onChange: (v: { isActive: boolean; fee: number }) => void;
}
interface IOwnState {
  isActive: boolean;
  fee: number;
}

class Fee extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    inputFee: 0,
    outputFee: 0,
    onChange: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isActive: false,
      fee: 0,
    };

    this.onChkChange = this.onChkChange.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
  }

  componentDidUpdate(prevProps: IOwnProps) {
    if (
      this.props.inputFee !== prevProps.inputFee ||
      this.props.outputFee !== prevProps.outputFee
    ) {
      this.setState(
        {
          isActive: this.props.inputFee === 0 ? false : true,
          fee: this.getFeePredict(),
        },
        () => {
          this.props.onChange(this.state);
        }
      );
    }
  }

  private onChkChange(value: boolean) {
    this.setState(
      (state: IOwnState) => ({
        isActive: value,
        fee: value ? state.fee : 0,
      }),
      () => {
        this.props.onChange(this.state);
      }
    );
  }

  private onInputChange(value: number) {
    this.setState(
      {
        fee: value,
      },
      () => {
        this.props.onChange(this.state);
      }
    );
  }

  private getFeePredict(): number {
    const { inputFee, outputFee } = this.props;
    if (inputFee === -1 || outputFee === -1) {
      return -1;
    }

    return inputFee + outputFee;
  }

  render(): ReactElement {
    const { isActive, fee } = this.state;
    const feePredict: number = this.getFeePredict();

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Fee</div>
        <div
          className={
            'd-flex align-items-center ' +
            swapHelper.getClassWarningFee(feePredict)
          }
        >
          {!isActive && (
            <span>{feePredict === -1 ? '?' : `(${feePredict}%)`}</span>
          )}
          {isActive && (
            <Input
              isAutoFocus={true}
              classInput={swapHelper.getClassWarningFee(fee)}
              title="%"
              value={fee === -1 ? 1 : fee}
              maxValue={TXN_FEE_MAX}
              onChange={this.onInputChange}
            />
          )}
          <div className={style.checkbox}>
            <Checkbox isCheck={isActive} onChkChange={this.onChkChange} />
          </div>
        </div>
      </div>
    );
  }
}

export default Fee;
