import _ from 'lodash';
import axios from 'axios';
import { toast } from 'react-toastify';
import { INVALID_ADDRESS } from '../config/web3Config';
import { ITokenSecurity } from '../model/tokenSecurityModel';

export async function getPairInfo(
  pairAddress: string
): Promise<ITokenSecurity> {
  let tokenSecurity: ITokenSecurity = {};
  const res: any = await axios
    .get(`https://api.dexscreener.com/latest/dex/pairs/bsc/${pairAddress}`)
    .catch((error) => {
      console.log(error);
      toast.error('[TOKEN SECURITY] Get pair info failure!', {
        autoClose: 500,
      });
    });

  tokenSecurity = mapDataPairInfoToTokenSecurity(res?.data?.pair);

  return tokenSecurity;
}

export async function getTokenSecurity(
  address: string
): Promise<ITokenSecurity> {
  let tokenSecurity: ITokenSecurity = {};
  const res: any = await axios
    .get(
      `https://api.gopluslabs.io/api/v1/token_security/56?contract_addresses=${address}`
    )
    .catch((error) => {
      console.log(error);
      toast.error('[TOKEN SECURITY] Get token security failure!', {
        autoClose: 500,
      });
    });

  tokenSecurity = mapDataSercurityToTokenSecurity(
    res?.data?.result?.[address]
  );

  return tokenSecurity;
}

function mapDataPairInfoToTokenSecurity(data: any = {}): ITokenSecurity {
  return {
    dexVersion: data?.labels?.[0],
    created: !_.isNil(data.pairCreatedAt)
      ? _.round((+new Date() - data.pairCreatedAt) / (24 * 60 * 60 * 1000))
      : undefined,
    vol: !_.isNil(data.volume?.h24) ? data.volume.h24 : undefined,
    tnx:
      !_.isNil(data.txns?.h24?.buys) && !_.isNil(data.txns?.h24?.sells)
        ? data.txns.h24.buys + data.txns.h24.sells
        : undefined,
    priceChange: !_.isNil(data.priceChange?.h24)
      ? _.round(data.priceChange.h24, 1)
      : undefined,
    fdv: !_.isNil(data.fdv) ? data.fdv : undefined,
  };
}

function mapDataSercurityToTokenSecurity(data: any = {}): ITokenSecurity {
  return {
    supply: !_.isEmpty(data.total_supply)
      ? _.round(Number(data.total_supply))
      : undefined,
    buyTax: !_.isEmpty(data.buy_tax)
      ? _.round(Number(data.buy_tax) * 100, 2)
      : undefined,
    sellTax: !_.isEmpty(data.sell_tax)
      ? _.round(Number(data.sell_tax) * 100, 2)
      : undefined,
    isTaxModifiable: computedBooleanFromString(data.slippage_modifiable),
    isHasBlacklist: computedBooleanFromString(data.is_blacklisted),
    isHasWhitelist: computedBooleanFromString(data.is_whitelisted),
    isMintable: computedBooleanFromString(data.is_mintable),
    isHoneypot: computedBooleanFromString(data.is_honeypot),
    isNotSellAll: computedBooleanFromString(data.cannot_sell_all),
    isTransferPausable: computedBooleanFromString(data.transfer_pausable),
    isOpenSource: computedBooleanFromString(data.is_open_source),
    isProxyContract: computedBooleanFromString(data.is_proxy),
    isBalanceModifiable: computedBooleanFromString(data.owner_change_balance),
    isExternalCall: computedBooleanFromString(data.external_call),
    lpHolderCount: !_.isEmpty(data.lp_holder_count)
      ? Number(data.lp_holder_count)
      : undefined,
    isHasOwner: data.owner_address === INVALID_ADDRESS ? false : true,
  };
}

function computedBooleanFromString(v?: string): boolean | undefined {
  if (v === '0') {
    return false;
  }
  if (v === '1') {
    return true;
  }

  return undefined;
}
