import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../../../config/tokenBep20Config';
import {
  TEMPLATE_INPUT_DOLAR,
  TEMPLATE_INPUT_BNB,
  TEMPLATE_INPUT_ALT,
} from '../../../../config/appSwapConfig';
import tokenHelper from '../../../../helper/tokenHelper';
import swapHelper from '../../../../helper/swapHelper';
import style from './InputCurrencyAmount.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable: boolean;
  isUiLoading: boolean;
  address: string;
  balance: string;
  price: number;
  liquidity: number;
  quoteSymbol: string;
  value: string;
  onChange: (v: string) => void;
}
interface IOwnState {}

class InputCurrencyAmount extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isDisable: false,
    isUiLoading: false,
    onChange: () => {},
  };

  state: IOwnState;

  private stableCoinAdress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
  ];
  private baseAddress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
    BNB_BEP20.address,
  ];

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
    this.onClearValue = this.onClearValue.bind(this);
    this.onMaxValue = this.onMaxValue.bind(this);
    this.onInputTemplateValue = this.onInputTemplateValue.bind(this);
    this.onInputTemplatePercentValue =
      this.onInputTemplatePercentValue.bind(this);
  }

  componentDidUpdate(prevProps: IOwnProps) {
    if (
      this.props.address !== prevProps.address &&
      !this.baseAddress.includes(this.props.address) &&
      !!Number(this.props.balance || 0) &&
      !this.props.isDisable
    ) {
      this.onMaxValue();
    }
  }

  private onChange(e: { target: { value: string } }) {
    const { onChange } = this.props;
    const value: string = e.target.value;
    const re = /^[0-9]*[.,]?[0-9]*$/;

    if (value.trim() === '') {
      onChange('');
    } else if (re.test(value)) {
      onChange(value.replace(',', '.'));
    }
  }

  private onClearValue() {
    this.props.onChange('');
  }

  private onMaxValue() {
    const { balance, onChange } = this.props;

    balance && onChange(balance);
  }

  private onInputTemplateValue(value: number) {
    this.props.onChange(value.toString());
  }

  private onInputTemplatePercentValue(percent: number) {
    const { balance, onChange } = this.props;
    const value: string = ((Number(balance || 0) * percent) / 100).toString();

    onChange(value);
  }

  private checkShowPrice(): boolean {
    return !this.stableCoinAdress.includes(this.props.address);
  }

  private checkShowLiquidity(): boolean {
    return !this.baseAddress.includes(this.props.address);
  }

  private buildPoolInfo(): string {
    const { isUiLoading, price, liquidity } = this.props;

    if (isUiLoading && this.checkShowPrice()) {
      return 'Loading...';
    }

    if (!this.checkShowPrice()) {
      return '';
    }

    if (!!price) {
      let poolInfo = `$${tokenHelper.roundCurrency(price)}`;

      if (this.checkShowLiquidity() && !!liquidity) {
        poolInfo += ` ☞ ${tokenHelper.convertToInternationalCurrencySystem(
          liquidity
        )}`;
      }

      return poolInfo;
    }

    if (_.isNil(liquidity)) {
      return 'Unknown!';
    }

    return '';
  }

  render(): ReactElement {
    const {
      isDisable,
      isUiLoading,
      address,
      balance,
      value,
      liquidity,
      quoteSymbol,
    } = this.props;

    return (
      <label className={style.wrap}>
        <input
          autoFocus
          inputMode="decimal"
          className={style.input}
          placeholder="0.0"
          disabled={isDisable}
          value={value}
          onChange={this.onChange}
        />
        <div className={style.info}>
          <div className='d-flex flex-column'>
            <span className={'fk-swap-text-info ' + style.pool}>{quoteSymbol}</span>
            <span
              className={
                'fk-swap-text-info ' +
                (isUiLoading ? '' : swapHelper.getClassPoolInfo(liquidity))
              }
            >
              {this.buildPoolInfo()}
            </span>
          </div>
          {!isDisable && (
            <div className="d-flex">
              <button
                disabled={isUiLoading || Number(value || 0) === 0}
                className={style.btnInput}
                onClick={this.onClearValue}
              >
                CL
              </button>
              {this.baseAddress.includes(address) &&
                (BNB_BEP20.address === address
                  ? TEMPLATE_INPUT_BNB
                  : TEMPLATE_INPUT_DOLAR
                ).map(
                  (inputValue: number): ReactElement => (
                    <button
                      key={inputValue}
                      disabled={Number(value || 0) === inputValue
                      }
                      className={style.btnInput}
                      onClick={() => this.onInputTemplateValue(inputValue)}
                    >
                      {inputValue}
                    </button>
                  )
                )}
              {!this.baseAddress.includes(address) &&
                TEMPLATE_INPUT_ALT.map(
                  (inputValue: number): ReactElement => (
                    <button
                      key={inputValue}
                      disabled={
                        isUiLoading ||
                        Number(value || 0) === Number(balance || 0) / 2
                      }
                      className={style.btnInput}
                      onClick={() =>
                        this.onInputTemplatePercentValue(inputValue)
                      }
                    >
                      {inputValue}%
                    </button>
                  )
                )}
              <button
                disabled={
                  isUiLoading || Number(balance || 0) === 0 || balance === value
                }
                className={style.btnInput}
                onClick={this.onMaxValue}
              >
                MAX
              </button>
            </div>
          )}
        </div>
      </label>
    );
  }
}

export default InputCurrencyAmount;
