import { PureComponent, ReactElement } from 'react';
import style from './ButtonBase.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  className: string;
  title: string;
  isDisable: boolean;
  onClick: () => void;
}
interface IOwnState {}

class ButtonBase extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    className: '',
    title: '',
    isDisable: false,
    onClick: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
    this.onClick = this.onClick.bind(this);
  }

  private onClick() {
    this.props.onClick && this.props.onClick();
  }

  render(): ReactElement {
    const { className, title, isDisable } = this.props;

    return (
      <button
        className={(className ? `${className} ` : '') + style.btn}
        disabled={isDisable}
        onClick={this.onClick}
      >
        {title}
      </button>
    );
  }
}

export default ButtonBase;
