import { PureComponent, ReactElement } from 'react';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import { Modal } from 'reactstrap';
import { TStoreState, dispatch } from '../../../store/config/store';
import { setPassphrase } from '../../../store/swapSettingActionCreater';
import cryptoHelper from '../../../helper/cryptoHelper';
import InputText from '../../controls/InputText/InputText';
import ButtonBase from '../../components/ButtonBase/ButtonBase';
import style from './ModalPassphrase.module.scss';

interface IStoreProps {
  adressWallet: string;
  mnemonicCrypto: string;
}
interface IHOCProps {}
interface IOwnProps {
  isOpen: boolean;
  onToggle: () => void;
  onEnter: (v: string) => void;
}
interface IOwnState {
  passphrase: string;
}

class ModalPassphrase extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isOpen: false,
    onToggle: () => {},
    onEnter: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = { passphrase: '' };

    this.onToggle = this.onToggle.bind(this);
    this.onEnter = this.onEnter.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
  }

  private onToggle() {
    this.props.onToggle();
  }

  private onEnter() {
    const { adressWallet, mnemonicCrypto } = this.props;
    const { passphrase } = this.state;
    const passphraseCrypto: string = !!passphrase
      ? cryptoHelper.encodeAES(passphrase)
      : passphrase;

    if (!!adressWallet) {
      const addressDecode = cryptoHelper
        .getWallet(mnemonicCrypto, passphraseCrypto)
        .address.toLowerCase();

      if (adressWallet !== addressDecode) {
        toast.error('Address wallet or mnemonic or passphrase is incorrect!');
      } else {
        this.raisePassphraseValid();
      }
    } else {
      this.raisePassphraseValid();
    }
  }

  private raisePassphraseValid() {
    const { onToggle, onEnter } = this.props;
    const { passphrase } = this.state;
    const passphraseCrypto: string = !!passphrase
      ? cryptoHelper.encodeAES(passphrase)
      : passphrase;

    dispatch(setPassphrase({ passphrase }));
    onToggle();
    onEnter(passphraseCrypto);
    this.setState({ passphrase: '' });
  }

  private onInputChange(v: string) {
    this.setState({ passphrase: v });
  }

  render(): ReactElement {
    const { isOpen } = this.props;
    const { passphrase } = this.state;

    return (
      <Modal
        centered
        className='fk-modal'
        isOpen={isOpen}
        fade={false}
        animation='false'
        toggle={this.onToggle}
      >
        <div className='d-flex justify-content-between p-4 header'>
          <span className='header-title'>Passphrase</span>
          <button className='fk-button-icon' onClick={this.onToggle}>
            <i className='bi bi-x-lg'></i>
          </button>
        </div>
        <div className='p-4 body'>
          <div className='my-4'>
            <InputText
              inputMode='numeric'
              className='fk-input-lg'
              placeholder='Enter Passphrase'
              value={passphrase}
              onChange={this.onInputChange}
            />
          </div>
        </div>
        <div className='d-flex justify-content-center p-4 footer'>
          <ButtonBase
            className={'mx-3 fk-button-primary ' + style.btn}
            title='Enter'
            onClick={this.onEnter}
          />
        </div>
      </Modal>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  adressWallet: state.swapSetting.adress,
  mnemonicCrypto: state.swapSetting.mnemonic,
});

export default connect(mapState)(ModalPassphrase);
