import { PureComponent, ReactElement } from 'react';
import logoBep20 from '../../../../asset/logo/bep20.png';
import style from './ButtonSellectCurrency.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable: boolean;
  isTokenInfoLoading?: boolean;
  symbol: string;
  address: string;
  logoURI: string;
  onClick: () => void;
}
interface IOwnState {}

class ButtonSellectCurrency extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isDisable: false,
    isTokenInfoLoading: false,
    onClick: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  render(): ReactElement {
    const { isDisable, isTokenInfoLoading, symbol, logoURI, onClick } =
      this.props;

    return (
      <button
        disabled={isDisable}
        className={style.selectBtn}
        onClick={onClick}
      >
        <img className={style.logo} src={logoURI || logoBep20} alt={''} />
        <span className={style.symbol}>{symbol}</span>
        {isTokenInfoLoading ? (
          <span className={style.loader}></span>
        ) : (
          <i className={`${style.iconDropdown} bi bi-chevron-compact-down`}></i>
        )}
      </button>
    );
  }
}

export default ButtonSellectCurrency;
