import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';
import { PANCAKE_FACTORY, APE_FACTORY } from '../../../config/web3Config';
import { DApp } from '../../../model/tokenModel';
import logoPancakewap from '../../../asset/logo/pancakeswap.png';
import logoApewap from '../../../asset/logo/apeswap.png';
import style from './ButtonSelectDApp.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable: boolean;
  dApp: DApp;
  onChange: (v: DApp) => void;
}
interface IOwnState {
  isOpen: boolean;
}

class ButtonSelectDApp extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isDisable: false,
    dApp: DApp.PANCAKE,
    onChange: (v: DApp) => {},
  };

  state: IOwnState;

  private dApps = [
    {
      dApp: DApp.PANCAKE,
      factory: PANCAKE_FACTORY,
      name: 'Pancake',
      logo: logoPancakewap,
    },
    {
      dApp: DApp.APE,
      factory: APE_FACTORY,
      name: 'Ape',
      logo: logoApewap,
    },
  ];

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isOpen: false,
    };

    this.onToggle = this.onToggle.bind(this);
    this.onItemClick = this.onItemClick.bind(this);
  }

  onToggle() {
    this.setState((state: IOwnState) => ({
      isOpen: !state.isOpen,
    }));
  }

  onItemClick(dApp: DApp) {
    this.props.onChange(dApp);
  }

  render(): ReactElement {
    const { isDisable, dApp } = this.props;
    const { isOpen } = this.state;
    const dAppSelected = _.find(this.dApps, { dApp }) || this.dApps[0];

    return (
      <Dropdown disabled={isDisable} isOpen={isOpen} toggle={this.onToggle}>
        <DropdownToggle
          className={
            'd-flex justify-content-center align-items-center p-0 shadow-none ' +
            style.dropdownToggle +
            (isDisable ? ' ' + style.disable : '')
          }
        >
          <img className={style.logo} src={dAppSelected.logo} alt='' />
          <span className={style.title}>{dAppSelected.name}</span>
        </DropdownToggle>
        <DropdownMenu className='fk-container'>
          {this.dApps
            .filter((v) => v.dApp !== dApp)
            .map((dApp) => (
              <DropdownItem
                key={dApp.dApp}
                className={'d-flex align-items-center ' + style.dropdownItem}
                onClick={() => this.onItemClick(dApp.dApp)}
              >
                <div className='d-flex align-items-center'>
                  <img className={style.logo} src={dApp.logo} alt='' />
                  <span>{dApp.name}</span>
                </div>
              </DropdownItem>
            ))}
        </DropdownMenu>
      </Dropdown>
    );
  }
}

export default ButtonSelectDApp;
