import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { Row, Col } from 'reactstrap';
import { TIME_REFRESH_TOKEN_SERCURITY } from '../../../config/appSwapConfig';
import tokenHelper from '../../../helper/tokenHelper';
import tokenSecurityClassHelper from '../../../helper/tokenSecurityClassHelper';
import { ITokenSecurity } from '../../../model/tokenSecurityModel';
import {
  getPairInfo,
  getTokenSecurity,
} from '../../../service/tokenSercurityService';
import YesCheck from './YesCheck/YesCheck';
import NoCheck from './NoCheck/NoCheck';
import style from './TokenSecurity.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  address: string;
  pairAddress: string;
  pairName: string;
}
interface IOwnState {
  tokenSecurity: ITokenSecurity;
  isMint: boolean;
  isLoading: boolean;
}

class PriceImpact extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    address: '',
    pairAddress: '',
    pairName: 'N/A',
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      tokenSecurity: {},
      isMint: false,
      isLoading: false,
    };

    this.onRefreshSercurityData = this.onRefreshSercurityData.bind(this);
  }

  componentDidMount() {
    this.loadSercurityData();

    setInterval(() => {
      this.loadSercurityData();
    }, TIME_REFRESH_TOKEN_SERCURITY);
  }

  async loadSercurityData() {
    const { address, pairAddress } = this.props;

    this.setState({
      isLoading: true,
    });

    const data: [ITokenSecurity, ITokenSecurity] = await Promise.all([
      getTokenSecurity(address),
      getPairInfo(pairAddress),
    ]);

    this.setState(
      (state: IOwnState) => ({
        tokenSecurity: {
          ...state.tokenSecurity,
          ...data[0],
          ...data[1],
        },
        isMint:
          !state.isMint && state.tokenSecurity.supply && data[0].supply
            ? data[0].supply > state.tokenSecurity.supply
            : false,
        isLoading: false,
      }),
      () => {}
    );
  }

  onRefreshSercurityData() {
    this.loadSercurityData();
  }

  render(): ReactElement {
    const { pairName } = this.props;
    const { isMint, isLoading } = this.state;
    const {
      dexVersion,
      created,
      vol,
      tnx,
      priceChange,
      fdv,
      supply,
      buyTax,
      sellTax,
      isTaxModifiable,
      isHasBlacklist,
      isHasWhitelist,
      isMintable,
      isHoneypot,
      isNotSellAll,
      isTransferPausable,
      isOpenSource,
      isProxyContract,
      isBalanceModifiable,
      isExternalCall,
      lpHolderCount,
      isHasOwner,
    } = this.state.tokenSecurity;

    return (
      <div className="h-100 px-3 py-4 p-md-4">
        <div className={style.title}>
          <span className="fk-swap-text-info">
            {dexVersion ? dexVersion.toLocaleUpperCase() : ''}
          </span>
          <span className={style.pairName}>{pairName}</span>
          <button
            className={style.btnRefresh}
            disabled={isLoading}
            onClick={this.onRefreshSercurityData}
          >
            {isLoading ? (
              <span className={style.loader}></span>
            ) : (
              <i className="bi bi-arrow-clockwise"></i>
            )}
          </button>
        </div>
        <div className={style.pairInfoWrap}>
          <Row>
            <Col xs={{ size: 4 }}>
              <div className={'p-2 ' + style.pairInfoItem}>
                <div>CREATED</div>
                <div className={style.pairInfoValue}>
                  {!_.isNil(created) ? created + 'D' : 'N/A'}
                </div>
              </div>
            </Col>
            <Col xs={{ size: 4 }}>
              <div className={'p-2 ' + style.pairInfoItem}>
                <div>VOL</div>
                <div className={style.pairInfoValue}>
                  {!_.isNil(vol)
                    ? tokenHelper.convertToInternationalCurrencySystem(vol)
                    : 'N/A'}
                </div>
              </div>
            </Col>
            <Col xs={{ size: 4 }}>
              <div className={'px-1 py-2 ' + style.pairInfoItem}>
                <div>TNX</div>
                <div className={style.pairInfoValue}>
                  {!_.isNil(tnx) ? tnx : 'N/A'}
                </div>
              </div>
            </Col>
            <Col xs={{ size: 4 }}>
              <div className={'p-2 ' + style.pairInfoItem}>
                <div>PRICE</div>
                <div
                  className={
                    style.pairInfoValue +
                    ' ' +
                    tokenSecurityClassHelper.getClassPrice(priceChange || 0)
                  }
                >
                  {!_.isNil(priceChange) ? priceChange + '%' : 'N/A'}
                </div>
              </div>
            </Col>
            <Col xs={{ size: 4 }}>
              <div className={'p-2 ' + style.pairInfoItem}>
                <div>FDV</div>
                <div className={style.pairInfoValue}>
                  {!_.isNil(fdv)
                    ? tokenHelper.convertToInternationalCurrencySystem(fdv)
                    : 'N/A'}
                </div>
              </div>
            </Col>
            <Col xs={{ size: 4 }}>
              <div className={'px-1 py-2 ' + style.pairInfoItem}>
                <div>SUPPLY</div>
                <div
                  className={
                    style.pairInfoValue + (isMint ? ' fk-text-red' : '')
                  }
                >
                  {!_.isNil(supply)
                    ? tokenHelper.convertToInternationalCurrencySystem(supply)
                    : 'N/A'}
                </div>
              </div>
            </Col>
          </Row>
        </div>
        <div className={style.tokenSecurityWrap}>
          <div className={style.tokenSecurityItem}>
            <span>Buy tax</span>
            <span
              className={
                style.tokenSecuritValue +
                ' ' +
                tokenSecurityClassHelper.getClassTax(buyTax)
              }
            >
              {!_.isNil(buyTax) ? buyTax + '%' : 'N/A'}
            </span>
          </div>
          <div className={style.tokenSecurityItem}>
            <span>Sell tax</span>
            <span
              className={
                style.tokenSecuritValue +
                ' ' +
                tokenSecurityClassHelper.getClassTax(sellTax)
              }
            >
              {!_.isNil(sellTax) ? sellTax + '%' : 'N/A'}
            </span>
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Tax modifiable</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(
                isTaxModifiable
              )}
              status={isTaxModifiable}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Has blacklist</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesWarning(
                isHasBlacklist
              )}
              status={isHasBlacklist}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Has whitelist</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesWarning(
                isHasWhitelist
              )}
              status={isHasWhitelist}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Mintable</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(isMintable)}
              status={isMintable}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Honeypot</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(isHoneypot)}
              status={isHoneypot}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Can't sell all</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(isNotSellAll)}
              status={isNotSellAll}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Transfer pausable</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(
                isTransferPausable
              )}
              status={isTransferPausable}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Open source</div>
            <YesCheck
              className={tokenSecurityClassHelper.getClassYesTrust(
                isOpenSource
              )}
              status={isOpenSource}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Proxy contract</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(
                isProxyContract
              )}
              status={isProxyContract}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Balance modifiable</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(
                isBalanceModifiable
              )}
              status={isBalanceModifiable}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <div>External call risk</div>
            <NoCheck
              className={tokenSecurityClassHelper.getClassYesRisk(
                isExternalCall
              )}
              status={isExternalCall}
            />
          </div>
          <div className={style.tokenSecurityItem}>
            <span>LP holder count</span>
            <span className={style.tokenSecuritValue}>
              {' '}
              {!_.isNil(lpHolderCount) ? lpHolderCount : 'N/A'}
            </span>
          </div>
          <div className={style.tokenSecurityItem}>
            <div>Has owner</div>
            <NoCheck
              className={
                _.isNil(isHasOwner) || isHasOwner ? '' : 'fk-text-green'
              }
              status={isHasOwner}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default PriceImpact;
