import { PureComponent, ReactElement } from 'react';
import copy from 'copy-to-clipboard';
import { BigNumber } from 'ethers';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../../config/tokenBep20Config';
import { MAX_UINT256 } from '../../../config/appSwapConfig';
import helper from '../../../helper/tokenHelper';
import { ITokenBase } from '../../../model/tokenModel';
import { ICurrencyInput } from '../../../model/swapModel';
import ButtonSellectCurrency from './ButtonSellectCurrency/ButtonSellectCurrency';
import InputCurrencyAmount from './InputCurrencyAmount/InputCurrencyAmount';
import ModalSelectToken from './ModalSelectToken/ModalSelectToken';
import style from './CurrencyInput.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable?: boolean;
  isTokenInfoLoading: boolean;
  isUiLoading: boolean;
  isProcessing: boolean;
  symbol: string;
  address: string;
  logoURI: string;
  balance: string;
  price: number;
  liquidity: number;
  quoteSymbol: string;
  value: string;
  onChange: (v: ICurrencyInput) => void;
}
interface IOwnState {
  isOpenModal: boolean;
  isCopied: boolean;
}

class CurrencyInput extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isTokenInfoLoading: false,
    isUiLoading: false,
    isProcessing: false,
    balance: 0,
    onChange: () => {},
  };

  state: IOwnState;

  private baseAdress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
    BNB_BEP20.address,
  ];

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isOpenModal: false,
      isCopied: false,
    };

    this.onCurrencySellectBtnClick = this.onCurrencySellectBtnClick.bind(this);
    this.onAmountChange = this.onAmountChange.bind(this);
    this.onModalSelectTokenToggle = this.onModalSelectTokenToggle.bind(this);
    this.onSelectToken = this.onSelectToken.bind(this);
    this.onCopyClipboard = this.onCopyClipboard.bind(this);
    this.onOpenLinkInfo = this.onOpenLinkInfo.bind(this);
  }

  onCurrencySellectBtnClick() {
    this.setState({ isOpenModal: true });
  }

  onAmountChange(value: string) {
    const { onChange } = this.props;

    onChange({ value } as ICurrencyInput);
  }

  onModalSelectTokenToggle() {
    this.setState({ isOpenModal: false });
  }

  onSelectToken(token: ITokenBase) {
    const { onChange } = this.props;

    onChange({
      symbol: token.symbol,
      name: token.name,
      address: token.address,
      decimals: token.decimals,
      logoURI: token.logoURI,
      burn: token.burn || 0,
      balance: '0',
      price: 0,
      liquidity: 0,
      value: '',
      allowance: BigNumber.from(MAX_UINT256).toString(),
    } as ICurrencyInput);
  }

  private onCopyClipboard() {
    copy(this.props.address);

    this.setState({ isCopied: true });

    setTimeout(() => {
      this.setState({ isCopied: false });
    }, 2000);
  }

  private onOpenLinkInfo() {
    const { address } = this.props;

    window.open(`https://dexscreener.com/bsc/${address}`);
    window.open(`https://poocoin.app/tokens/${address}`);
    window.open(`https://tokensniffer.com/token/${address}`);
    window.open(`https://www.coingecko.com/en/coins/${address}`);
  }

  render(): ReactElement {
    const {
      isDisable,
      isTokenInfoLoading,
      isUiLoading,
      isProcessing,
      symbol,
      address,
      logoURI,
      value,
      balance,
      price,
      liquidity,
      quoteSymbol,
    } = this.props;
    const { isOpenModal, isCopied } = this.state;

    return (
      <div className={style.wrap}>
        <div className={style.title}>
          <div>
            <div className={style.wrapBtnSellect}>
              <ButtonSellectCurrency
                isDisable={isProcessing || isTokenInfoLoading}
                isTokenInfoLoading={isTokenInfoLoading}
                symbol={symbol}
                address={address}
                logoURI={logoURI}
                onClick={this.onCurrencySellectBtnClick}
              />
            </div>
            {this.baseAdress.includes(address) ? (
              ''
            ) : isCopied ? (
              <button
                className={`${style.button} ${style.check}`}
                title="Copied!"
              >
                <i className="bi bi-check2"></i>
              </button>
            ) : (
              <button
                className={style.button}
                title="Copy contract to clipboard"
                onClick={this.onCopyClipboard}
              >
                <i className="bi bi-clipboard"></i>
              </button>
            )}
            {this.baseAdress.includes(address) ? (
              ''
            ) : (
              <button
                className={style.button}
                title="Check info!"
                onClick={this.onOpenLinkInfo}
              >
                <i className="bi bi-info-lg"></i>
              </button>
            )}
          </div>
          <span className={'fk-swap-text-info ' + style.balance}>
            {isUiLoading
              ? 'Loading...'
              : 'Balance: ' +
                helper.convertBalanceToInternationalCurrencySystem(
                  Number(balance)
                )}
          </span>
        </div>
        <InputCurrencyAmount
          isDisable={isDisable}
          isUiLoading={isUiLoading}
          address={address}
          balance={balance}
          price={price}
          liquidity={liquidity}
          quoteSymbol={quoteSymbol}
          value={value}
          onChange={this.onAmountChange}
        />
        <ModalSelectToken
          isOpen={isOpenModal}
          address={address}
          onToggle={this.onModalSelectTokenToggle}
          onSelect={this.onSelectToken}
        />
      </div>
    );
  }
}

export default CurrencyInput;
