import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { Modal } from 'reactstrap';
import {
  TXN_TIMEOUT,
  TXN_GAS,
  TXN_GAS_PRICE_MAX,
  TXN_SLIP_MAX,
} from '../../../config/appSwapConfig';
import { TStoreState, dispatch } from '../../../store/config/store';
import {
  TSwapSetting,
  TTxnPriority,
  swapSettingDefault,
} from '../../../store/swapSettingReducer';
import {
  updateSwapSetting,
  resetSwapSetting,
} from '../../../store/swapSettingActionCreater';
import ModalConfirm from '../../components/ModalConfirm/ModalConfirm';
import InputText from '../../controls/InputText/InputText';
import ButtonBase from '../../components/ButtonBase/ButtonBase';
import style from './ModalSetting.module.scss';

interface IStoreProps {
  swapSetting: TSwapSetting;
}
interface IHOCProps {}
interface IOwnProps {
  isOpen: boolean;
  onToggle: () => void;
}
interface IOwnState {
  isOpenResetConfirm: boolean;
  setting:
    | {
        [k: string]: any;
      }
    | TSwapSetting;
}

class ModalSetting extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isOpen: true,
    onToggle: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isOpenResetConfirm: false,
      setting: props.swapSetting,
    };

    this.onToggle = this.onToggle.bind(this);
    this.onResetToggle = this.onResetToggle.bind(this);
    this.onReset = this.onReset.bind(this);
    this.onResetConfirm = this.onResetConfirm.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
  }

  componentDidUpdate(prevProps: IStoreProps) {
    if (prevProps.swapSetting.mnemonic !== this.props.swapSetting.mnemonic) {
      this.setState((state: IOwnState) => ({
        setting: {
          ...state.setting,
          mnemonic: this.props.swapSetting.mnemonic,
        },
      }));
    }
  }

  private onToggle() {
    this.setState((state: IOwnState, props: IStoreProps) => ({
      setting: props.swapSetting,
    }));

    this.props.onToggle();
  }

  private onResetToggle() {
    this.setState((state: IOwnState) => ({
      isOpenResetConfirm: !state.isOpenResetConfirm,
    }));
  }

  private onReset() {
    this.setState((state: IOwnState) => ({
      isOpenResetConfirm: !state.isOpenResetConfirm,
    }));
  }

  private onResetConfirm() {
    dispatch(resetSwapSetting());

    this.setState((state: IOwnState) => ({
      isOpenResetConfirm: !state.isOpenResetConfirm,
      setting: swapSettingDefault,
    }));
  }

  private onSave() {
    dispatch(updateSwapSetting(this.state.setting));

    this.props.onToggle();
  }

  private onInputChange(name: string, v: string | number | TTxnPriority) {
    this.setState((state: IOwnState) => ({
      setting: {
        ...state.setting,
        [name]: v,
      },
    }));
  }

  render(): ReactElement {
    const { isOpen, swapSetting } = this.props;
    const { isOpenResetConfirm } = this.state;
    const {
      mnemonic,
      adress,
      proxy,
      tnxTimeout,
      tnxGas,
      txnPriorityLow,
      txnPriorityMedium,
      txnPriorityHight,
      defaultTokenInput,
    } = this.state.setting;

    return (
      <Modal
        centered
        className='fk-modal'
        isOpen={isOpen}
        fade={false}
        animation='false'
        toggle={this.onToggle}
      >
        <div className='d-flex justify-content-between p-4 header'>
          <span className='header-title'>Setting</span>
          <button className='fk-button-icon' onClick={this.onToggle}>
            <i className='bi bi-x-lg'></i>
          </button>
        </div>
        <div className='p-4 body'>
          {/* START SECTION 1 */}
          <div className='pb-4'>
            <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Mnemonic *</div>
              <InputText
                className='fk-input-lg'
                placeholder='Enter words seperate with spaces'
                value={mnemonic}
                onChange={(v: string) => this.onInputChange('mnemonic', v)}
              />
            </div>
            <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Wallet address</div>
              <InputText
                className='fk-input-lg'
                placeholder='Enter address wallet'
                value={adress}
                onChange={(v: string) => this.onInputChange('adress', v)}
              />
            </div>
            <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Proxy *</div>
              <InputText
                className='fk-input-lg'
                placeholder='Enter address proxy'
                value={proxy}
                onChange={(v: string) => this.onInputChange('proxy', v)}
              />
            </div>
          </div>
          {/* START SECTION 2 */}
          <div className='d-flex justify-content-between pb-4'>
            <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Tnx Timeout</div>
              <InputText
                inputMode='numeric'
                className='fk-input-lg'
                placeholder={`~ ${TXN_TIMEOUT}`}
                value={tnxTimeout ? tnxTimeout.toString() : ''}
                onChange={(v: string) =>
                  this.onInputChange(
                    'tnxTimeout',
                    _.isNaN(Number(v)) ? tnxTimeout : Number(v)
                  )
                }
              />
            </div>
            <div className='mx-3'></div>
            <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Tnx Gas *</div>
              <InputText
                inputMode='numeric'
                className='fk-input-lg'
                placeholder={`>= ${TXN_GAS}`}
                value={tnxGas ? tnxGas.toString() : ''}
                onChange={(v: string) =>
                  this.onInputChange(
                    'tnxGas',
                    _.isNaN(Number(v)) ? tnxGas : Number(v)
                  )
                }
              />
            </div>
          </div>
          {/* START SECTION 3 */}
          <div className='pb-4'>
            <div className='d-flex justify-content-between'>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>Gas Price (Low) *</div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[5, ${TXN_GAS_PRICE_MAX}]`}
                  value={
                    txnPriorityLow.gasPrice
                      ? txnPriorityLow.gasPrice.toString()
                      : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityLow',
                      _.isNaN(Number(v))
                        ? txnPriorityLow
                        : { ...txnPriorityLow, gasPrice: Number(v) }
                    )
                  }
                />
              </div>
              <div className='mx-3'></div>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>Slip (Low) *</div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[1, ${TXN_SLIP_MAX}]`}
                  value={
                    txnPriorityLow.slip ? txnPriorityLow.slip.toString() : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityLow',
                      _.isNaN(Number(v))
                        ? txnPriorityLow
                        : { ...txnPriorityLow, slip: Number(v) }
                    )
                  }
                />
              </div>
            </div>
            <div className='d-flex justify-content-between'>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>
                  Gas Price (Medium) *
                </div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[5, ${TXN_GAS_PRICE_MAX}]`}
                  value={
                    txnPriorityMedium.gasPrice
                      ? txnPriorityMedium.gasPrice.toString()
                      : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityMedium',
                      _.isNaN(Number(v))
                        ? txnPriorityMedium
                        : { ...txnPriorityMedium, gasPrice: Number(v) }
                    )
                  }
                />
              </div>
              <div className='mx-3'></div>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>Slip (Medium) *</div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[1, ${TXN_SLIP_MAX}]`}
                  value={
                    txnPriorityMedium.slip
                      ? txnPriorityMedium.slip.toString()
                      : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityMedium',
                      _.isNaN(Number(v))
                        ? txnPriorityMedium
                        : { ...txnPriorityMedium, slip: Number(v) }
                    )
                  }
                />
              </div>
            </div>
            <div className='d-flex justify-content-between'>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>
                  Gas Price (Hight) *
                </div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[5, ${TXN_GAS_PRICE_MAX}]`}
                  value={
                    txnPriorityHight.gasPrice
                      ? txnPriorityHight.gasPrice.toString()
                      : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityHight',
                      _.isNaN(Number(v))
                        ? txnPriorityHight
                        : { ...txnPriorityHight, gasPrice: Number(v) }
                    )
                  }
                />
              </div>
              <div className='mx-3'></div>
              <div className='mb-3'>
                <div className='mb-1 fk-input-label-lg'>Slip (Hight) *</div>
                <InputText
                  inputMode='numeric'
                  className='fk-input-lg'
                  placeholder={`[1, ${TXN_SLIP_MAX}]`}
                  value={
                    txnPriorityHight.slip
                      ? txnPriorityHight.slip.toString()
                      : ''
                  }
                  onChange={(v: string) =>
                    this.onInputChange(
                      'txnPriorityHight',
                      _.isNaN(Number(v))
                        ? txnPriorityHight
                        : { ...txnPriorityHight, slip: Number(v) }
                    )
                  }
                />
              </div>
            </div>
          </div>
           {/* START SECTION 4 */}
          <div className='mb-3'>
              <div className='mb-1 fk-input-label-lg'>Default Token Input</div>
              <InputText
                className='fk-input-lg'
                placeholder='Enter address token'
                value={defaultTokenInput}
                onChange={(v: string) => this.onInputChange('defaultTokenInput', v)}
              />
            </div>
        </div>
        <div className='d-flex justify-content-end p-4 footer'>
          <ButtonBase
            className={'mx-3 fk-button-secondary ' + style.btn}
            title='Reset'
            isDisable={_.isEqual(this.state.setting, swapSettingDefault)}
            onClick={this.onReset}
          />
          <ButtonBase
            className={'fk-button-primary ' + style.btn}
            title='Save'
            isDisable={_.isEqual(this.state.setting, swapSetting)}
            onClick={this.onSave}
          />
        </div>
        <ModalConfirm
          title='Reset setting'
          contents={[
            'This action will delete all data, and reset the settings to default. Are you sure?',
          ]}
          isOpen={isOpenResetConfirm}
          onToggle={this.onResetToggle}
          onConfirm={this.onResetConfirm}
        />
      </Modal>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  swapSetting: state.swapSetting,
});

export default connect(mapState)(ModalSetting);
