import _ from 'lodash';
import style from './YesCheck.module.scss';

export default function YesCheck(props: {
  className?: string;
  status?: boolean;
}) {
  return (
    <div
      className={style.wrap + (!!props.className ? ' ' + props.className : '')}
    >
      <span className={style.text}>
        {_.isNil(props.status) ? 'N/A' : props.status ? 'YES' : 'NO'}
      </span>
      {_.isNil(props.status) || props.status ? (
        <i className="bi bi-check-circle-fill"></i>
      ) : (
        <i className="bi bi-exclamation-triangle-fill"></i>
      )}
    </div>
  );
}
