import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import { BigNumber, Wallet } from 'ethers';
import { connect } from 'react-redux';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { Container, Row, Col } from 'reactstrap';
import { toast } from 'react-toastify';
import {
  UPDATE_TOKEN_DELAY,
  TXN_PRIORITY_LOW,
  MAX_UINT256,
  ERROR_UNPREDICTABLE_GAS_LIMIT,
  SAFE_SWAP,
} from '../../config/appSwapConfig';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../config/tokenBep20Config';
import { IRoutedProps, withRouter } from '../../lib/HOC';
import { TStoreState, dispatch } from '../../store/config/store';
import { TSwapSetting } from '../../store/swapSettingReducer';
import helper from '../../helper/tokenHelper';
import swapHelper from '../../helper/swapHelper';
import cryptoHelper from '../../helper/cryptoHelper';
import { DApp, ITokenBase, ITokenDetail, IPool } from '../../model/tokenModel';
import {
  ConfirmStatus,
  TnxStatus,
  ICurrencyInput,
  IRouteSwap,
  IPriceImpact,
  ITnxReciept,
  ITnxConfig,
} from '../../model/swapModel';
import {
  clearPassphrase,
  setConnectStatus,
} from '../../store/swapSettingActionCreater';
import loadingService from '../../service/loadingService';
import {
  getOutputTokenInfo,
  getTokenInfos,
} from '../../service/tokenInfoService';
import { TPoolAddressReserve } from '../../service/tokenPoolService';
import { mappingModalSelectToken } from '../../service/swapMappingService';
import swapService from '../../service/swapService';
import ModalSetting from './ModalSetting/ModalSetting';
import ModalPassphrase from './ModalPassphrase/ModalPassphrase';
import ModalConfirm from '../components/ModalConfirm/ModalConfirm';
import ToastLink from '../../view/components/ToastLink/ToastLink';
import ButtonWalletConnected from './ButtonWalletConnected/ButtonWalletConnected';
import ButtonSelectDApp from './ButtonSelectDApp/ButtonSelectDApp';
import ButtonSetting from './ButtonSetting/ButtonSetting';
import CurrencyInput from './CurrencyInput/CurrencyInput';
import PriceImpact from './PriceImpact/PriceImpact';
import RouteSwap from './RouteSwap/RouteSwap';
import Fee from './Fee/Fee';
import GasSlip from './GasSlip/GasSlip';
import ButtonSwapPosition from './ButtonSwapPosition/ButtonSwapPosition';
import ButtonBase from '../components/ButtonBase/ButtonBase';
import TokenSecurity from './TokenSecurity/TokenSecurity';
import style from './Swap.module.scss';

enum TnxAction {
  APPROVE = 'Approve',
  SWAP = 'Swap',
}
interface IStoreProps {
  swapSetting: TSwapSetting;
  tokens: ITokenBase[];
  bnbPrice: number;
}
interface IHOCProps extends IRoutedProps {}
interface IOwnProps {}
interface IOwnState {
  wallet: Wallet;
  dApp: DApp;
  routeSwaps: IRouteSwap[];
  priceImpact: number;
  input: ICurrencyInput;
  output: ICurrencyInput;
  tokenSecurity: {
    address?: string;
    pairAddress?: string;
    pairName?: string;
  };
  confirmSwapMessages: string[];
  isOpenSetting: boolean;
  isOpenPassphrase: boolean;
  isOpenSwapConfirm: boolean;
  isTokenInfoLoading: boolean;
  isUiLoading: boolean;
  isProcessing: boolean;
}

class Swap extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  private stopRecursion = false;
  private stableCoinAdress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
  ];
  private baseAddress = [
    BUSD_BEP20.address,
    USDT_BEP20.address,
    USDC_BEP20.address,
    BNB_BEP20.address,
  ];
  private tokenInput: ITokenDetail = {} as ITokenDetail;
  private tokenOutput: ITokenDetail = {} as ITokenDetail;
  private tnxConfig: ITnxConfig;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    const defaultTokenInput: ITokenBase =
      _.find([BUSD_BEP20, USDT_BEP20, USDC_BEP20, BNB_BEP20], {
        address: props.swapSetting.defaultTokenInput,
      }) || USDT_BEP20;
    const defaultTokenOutput: ITokenBase =
      defaultTokenInput.address === USDT_BEP20.address ? BNB_BEP20 : USDT_BEP20;

    this.state = {
      wallet: {} as Wallet,
      dApp: DApp.PANCAKE,
      routeSwaps: [
        {
          symbol: defaultTokenInput.symbol as string,
          address: defaultTokenInput.address,
        },
        {
          symbol: defaultTokenOutput.symbol as string,
          address: defaultTokenOutput.address,
        },
      ],
      priceImpact: 0,
      input: {
        ...(defaultTokenInput as ICurrencyInput),
        burn: 0,
        balance: '0',
        price: 0,
        liquidity: 0,
        quoteSymbol: '',
        value: '',
        allowance: BigNumber.from(MAX_UINT256).toString(),
      },
      output: {
        ...(defaultTokenOutput as ICurrencyInput),
        burn: 0,
        balance: '0',
        price: 0,
        liquidity: 0,
        quoteSymbol: '',
        value: '',
        allowance: BigNumber.from(MAX_UINT256).toString(),
      },
      tokenSecurity: {},
      confirmSwapMessages: [],
      isOpenSetting: false,
      isOpenPassphrase: false,
      isOpenSwapConfirm: false,
      isTokenInfoLoading: false,
      isUiLoading: false,
      isProcessing: false,
    };

    this.tnxConfig = {
      proxy: props.swapSetting.proxy,
      tnxGas: props.swapSetting.tnxGas,
      gasPrice: TXN_PRIORITY_LOW.gasPrice,
      slip: TXN_PRIORITY_LOW.slip,
      fee: {
        isActive: false,
        fee: 0,
      },
    };

    this.onModalSettingToggle = this.onModalSettingToggle.bind(this);
    this.onModalPassphraseToggle = this.onModalPassphraseToggle.bind(this);
    this.onModalPassphraseEnter = this.onModalPassphraseEnter.bind(this);
    this.onConnectWallet = this.onConnectWallet.bind(this);
    this.onDisconnectWallet = this.onDisconnectWallet.bind(this);
    this.onSelectDApp = this.onSelectDApp.bind(this);
    this.onCurrencyInputChange = this.onCurrencyInputChange.bind(this);
    this.onCurrencyOutputChange = this.onCurrencyOutputChange.bind(this);
    this.onFeeSettingChange = this.onFeeSettingChange.bind(this);
    this.onGasSlipSettingChange = this.onGasSlipSettingChange.bind(this);
    this.onSwapPosition = this.onSwapPosition.bind(this);
    this.onEnableToken = this.onEnableToken.bind(this);
    this.onSafeSwap = this.onSafeSwap.bind(this);
    this.onSwapConfirmToggle = this.onSwapConfirmToggle.bind(this);
    this.onSwapConfirm = this.onSwapConfirm.bind(this);
    this.swapToken = this.swapToken.bind(this);
  }

  async componentDidMount() {
    const { params } = this.props;
    const { isConnected } = this.props.swapSetting;

    isConnected && this.initWallet(undefined, true);

    this.initTokenOutput();
    if (params.address) {
      const { tokens } = this.props;
      const { input } = this.state;
      const tokenInput = _.find(tokens, {
        address: input.address,
      }) as ITokenDetail;
      const tokenOutput = _.find(tokens, {
        address: params.address,
      }) as ITokenDetail;
      this.setState({
        isUiLoading: true,
      });

      const lazyData: [void, TPoolAddressReserve] = await Promise.all([
        this.initOutputTokenInfos(),
        swapService.getTokenPoolReserveSwaps([tokenInput, tokenOutput]),
      ]);

      this.setState({
        isUiLoading: true,
      });

      this.initSwapTokenFromPoolReserve(lazyData[1]);
      getTokenInfos();
    } else {
      await this.initTokenInfos();
      await this.initSwapToken();
    }

    this.initTokenOutput();
    this.recursionUpdateSwapToken();
  }

  componentDidUpdate(prevProps: IStoreProps) {
    if (
      (this.props.swapSetting.mnemonic !== prevProps.swapSetting.mnemonic &&
        prevProps.swapSetting.mnemonic !== '') ||
      this.props.swapSetting.proxy !== prevProps.swapSetting.proxy
    ) {
      window.location.reload();
    }
    if (this.props.swapSetting.proxy !== prevProps.swapSetting.proxy) {
      this.tnxConfig = {
        ...this.tnxConfig,
        proxy: this.props.swapSetting.proxy,
      };
    }
    if (this.props.swapSetting.tnxGas !== prevProps.swapSetting.tnxGas) {
      this.tnxConfig = {
        ...this.tnxConfig,
        tnxGas: this.props.swapSetting.tnxGas,
      };
    }
  }

  componentWillUnmount() {
    this.stopRecursion = true;
  }

  private initWallet(passphrase?: string, isFirstRun?: boolean) {
    const { mnemonic: mnemonicCrypto } = this.props.swapSetting;
    const passphraseCrypto = passphrase || this.props.swapSetting.passphrase;

    if (!!mnemonicCrypto && (!!passphraseCrypto || passphraseCrypto === '')) {
      const wallet: Wallet = cryptoHelper.getWallet(
        mnemonicCrypto,
        passphraseCrypto
      );
      this.setState({ wallet }, () => {
        if (!isFirstRun) {
          this.updateTokenBalance();
          this.updateTokenAllowances();
        }
      });
    }
  }

  private async initOutputTokenInfos() {
    const { params } = this.props;

    this.setState({
      isTokenInfoLoading: true,
    });

    await getOutputTokenInfo(params.address);

    this.setState({
      isTokenInfoLoading: false,
    });
  }

  private async initTokenInfos() {
    this.setState({
      isUiLoading: true,
    });
    loadingService.showLoading();
    await getTokenInfos();
    loadingService.hideLoading();
  }

  private initTokenOutput() {
    const { params, tokens } = this.props;

    if (params.address) {
      const tokenOutput: ITokenBase = _.find(tokens, {
        address: params.address.toLowerCase(),
      }) as ITokenBase;

      if (tokenOutput) {
        this.setState((state: IOwnState) => ({
          output: {
            ...state.output,
            symbol: (tokenOutput.symbol as string) || '',
            name: tokenOutput.name,
            address: tokenOutput.address,
            decimals: (tokenOutput.decimals as number) || 18,
            logoURI: tokenOutput.logoURI as string,
            burn: tokenOutput.burn || 0,
          },
        }));
      }
    }
  }

  private initSwapTokenFromPoolReserve(
    poolAddressReserve: TPoolAddressReserve
  ) {
    const { tokens, bnbPrice } = this.props;
    const { input, output } = this.state;
    let tokenSwaps: [ITokenDetail, ITokenDetail];
    let dApp: DApp;
    let routeSwaps: IRouteSwap[];
    let priceImpact: IPriceImpact;

    this.tokenInput = _.find(tokens, {
      address: input.address,
    }) as ITokenDetail;
    this.tokenOutput = _.find(tokens, {
      address: output.address,
    }) as ITokenDetail;

    this.setState({
      isUiLoading: true,
    });

    tokenSwaps = swapService.mapTokenPoolFromReserveSwaps(
      [this.tokenInput, this.tokenOutput],
      poolAddressReserve
    );

    this.tokenInput = tokenSwaps[0];
    this.tokenOutput = tokenSwaps[1];
    dApp = [
      this.tokenInput.targetPool?.dApp,
      this.tokenOutput.targetPool?.dApp,
    ].includes(DApp.APE)
      ? DApp.APE
      : DApp.PANCAKE;

    this.resetTargetPoolPerDApp(dApp);

    routeSwaps = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      dApp
    );
    priceImpact = swapService.getPriceImpact(
      Number(input.value || 0),
      this.tokenInput,
      this.tokenOutput,
      routeSwaps,
      bnbPrice
    );

    this.setState(
      (state: IOwnState) => ({
        dApp,
        routeSwaps,
        priceImpact: priceImpact.priceImpact,
        input: {
          ...state.input,
          price: this.tokenInput.price as number,
          liquidity: this.tokenInput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenInput.targetPool?.quoteSymbol as string,
        },
        output: {
          ...state.output,
          price: this.tokenOutput.price as number,
          liquidity: this.tokenOutput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenOutput.targetPool?.quoteSymbol as string,
        },
        tokenSecurity: {
          address: this.tokenOutput.address,
          pairAddress: this.tokenOutput.targetPool?.address.toLowerCase(),
          pairName: `${this.tokenOutput.symbol}/${this.tokenOutput.targetPool?.quoteSymbol}`,
        },
        isUiLoading: false,
      }),
      () => {
        this.updateTokenBalance();
        this.updateTokenAllowances();
      }
    );
  }

  private async initSwapToken() {
    const { tokens, bnbPrice } = this.props;
    const { input, output } = this.state;
    let tokenSwaps: [ITokenDetail, ITokenDetail];
    let dApp: DApp;
    let routeSwaps: IRouteSwap[];
    let priceImpact: IPriceImpact;

    this.tokenInput = _.find(tokens, {
      address: input.address,
    }) as ITokenDetail;
    this.tokenOutput = _.find(tokens, {
      address: output.address,
    }) as ITokenDetail;

    this.setState({
      isUiLoading: true,
    });

    tokenSwaps = await swapService.getTokenPoolSwaps([
      this.tokenInput,
      this.tokenOutput,
    ]);

    this.tokenInput = tokenSwaps[0];
    this.tokenOutput = tokenSwaps[1];
    dApp = [
      this.tokenInput.targetPool?.dApp,
      this.tokenOutput.targetPool?.dApp,
    ].includes(DApp.APE)
      ? DApp.APE
      : DApp.PANCAKE;

    this.resetTargetPoolPerDApp(dApp);

    routeSwaps = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      dApp
    );
    priceImpact = swapService.getPriceImpact(
      Number(input.value || 0),
      this.tokenInput,
      this.tokenOutput,
      routeSwaps,
      bnbPrice
    );

    this.setState(
      (state: IOwnState) => ({
        dApp,
        routeSwaps,
        priceImpact: priceImpact.priceImpact,
        input: {
          ...state.input,
          price: this.tokenInput.price as number,
          liquidity: this.tokenInput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenInput.targetPool?.quoteSymbol as string,
        },
        output: {
          ...state.output,
          price: this.tokenOutput.price as number,
          liquidity: this.tokenOutput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenOutput.targetPool?.quoteSymbol as string,
        },
        isUiLoading: false,
      }),
      () => {
        this.updateTokenBalance();
        this.updateTokenAllowances();
      }
    );
  }

  private resetTargetPoolPerDApp(dApp: DApp) {
    let tokenInputTargetPool: IPool;
    let tokenOutputTargetPool: IPool;
    if (!_.includes(this.stableCoinAdress, this.tokenInput.address)) {
      tokenInputTargetPool = _.chain(this.tokenInput.pools)
        .filter({ dApp })
        .maxBy('liquidity')
        .value();
      this.tokenInput = {
        ...this.tokenInput,
        price: tokenInputTargetPool?.reservePrice,
        targetPool: tokenInputTargetPool,
      };
    }
    if (!_.includes(this.stableCoinAdress, this.tokenOutput.address)) {
      tokenOutputTargetPool = _.chain(this.tokenOutput.pools)
        .filter({ dApp })
        .maxBy('liquidity')
        .value();
      this.tokenOutput = {
        ...this.tokenOutput,
        price: tokenOutputTargetPool?.reservePrice,
        targetPool: tokenOutputTargetPool,
      };
    }
  }

  private async recursionUpdateSwapToken() {
    if (this.stopRecursion) {
      return;
    }

    await this.updateSwapToken();
    await new Promise((r) => setTimeout(r, UPDATE_TOKEN_DELAY));
    await this.recursionUpdateSwapToken();
  }

  private async updateSwapToken() {
    const tokenInputSnapshot: ITokenDetail = this.tokenInput;
    const tokenOutputSnapshot: ITokenDetail = this.tokenOutput;
    const dApp: DApp = this.state.dApp;
    const tokenSwaps: [ITokenDetail, ITokenDetail] =
      await swapService.getTokenSwaps(
        [tokenInputSnapshot, tokenOutputSnapshot],
        dApp
      );

    if (
      !_.includes(this.stableCoinAdress, this.tokenInput.address) &&
      tokenInputSnapshot.address === this.tokenInput.address
    ) {
      this.tokenInput = tokenSwaps[0];
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          price: this.tokenInput.price as number,
          liquidity: this.tokenInput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenInput.targetPool?.quoteSymbol as string,
        },
      }));
    }

    if (
      !_.includes(this.stableCoinAdress, this.tokenOutput.address) &&
      tokenOutputSnapshot.address === this.tokenOutput.address
    ) {
      this.tokenOutput = tokenSwaps[1];
      this.setState((state: IOwnState) => ({
        output: {
          ...state.output,
          price: this.tokenOutput.price as number,
          liquidity: this.tokenOutput.targetPool?.liquidity as number,
          quoteSymbol: this.tokenOutput.targetPool?.quoteSymbol as string,
        },
      }));
    }

    if (
      tokenInputSnapshot.address === this.tokenInput.address &&
      !!this.tokenInput.price &&
      tokenOutputSnapshot.address === this.tokenOutput.address &&
      !!this.tokenOutput.price
    ) {
      const priceImpact: IPriceImpact = swapService.getPriceImpact(
        Number(this.state.input.value || 0),
        this.tokenInput,
        this.tokenOutput,
        this.state.routeSwaps,
        this.props.bnbPrice
      );

      this.setState((state: IOwnState) => ({
        priceImpact: priceImpact.priceImpact,
        output: {
          ...state.output,
          value: priceImpact.amountOut ? priceImpact.amountOut : '',
        },
      }));
    }
  }

  private async updateTokenBalance() {
    if (_.isEmpty(this.tokenInput) || _.isEmpty(this.tokenOutput)) {
      return;
    }

    if (_.isEmpty(this.state.wallet)) {
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          balance: '0',
          value: '0',
        },
        output: {
          ...state.output,
          balance: '0',
          value: '0',
        },
      }));

      return;
    }

    this.setState({ isUiLoading: true });

    const tokenInputSnapshot: ITokenDetail = this.tokenInput;
    const tokenOutputSnapshot: ITokenDetail = this.tokenOutput;
    const walletAddress: string = this.state.wallet.address;
    const balances: [string, string] = await Promise.all([
      swapService.getTokenBalanceOfWallet(
        walletAddress,
        tokenInputSnapshot.address,
        tokenInputSnapshot.decimals
      ),
      swapService.getTokenBalanceOfWallet(
        walletAddress,
        tokenOutputSnapshot.address,
        tokenOutputSnapshot.decimals
      ),
    ]);

    if (tokenInputSnapshot.address === this.tokenInput.address) {
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          balance: balances[0],
        },
        isUiLoading: false,
      }));
    }

    if (tokenOutputSnapshot.address === this.tokenOutput.address) {
      this.setState((state: IOwnState) => ({
        output: {
          ...state.output,
          balance: balances[1],
        },
        isUiLoading: false,
      }));
    }
  }

  private async updateTokenAllowances() {
    if (_.isEmpty(this.tokenInput) || _.isEmpty(this.tokenOutput)) {
      return;
    }

    if (_.isEmpty(this.state.wallet)) {
      this.setState((state: IOwnState) => ({
        input: {
          ...state.input,
          allowance: BigNumber.from(MAX_UINT256).toString(),
        },
        output: {
          ...state.output,
          allowance: BigNumber.from(MAX_UINT256).toString(),
        },
      }));

      return;
    }

    this.setState({ isUiLoading: true });

    const { swapSetting } = this.props;
    const { wallet, input, output } = this.state;
    const allowances: [string, string] = await Promise.all([
      swapService.getTokenAllowance(
        wallet.address,
        input.address,
        swapSetting.proxy
      ),
      swapService.getTokenAllowance(
        wallet.address,
        output.address,
        swapSetting.proxy
      ),
    ]);

    this.setState((state: IOwnState) => ({
      isUiLoading: false,
      input: {
        ...state.input,
        allowance: allowances[0],
      },
      output: {
        ...state.output,
        allowance: allowances[1],
      },
    }));
  }

  private onConnectWallet() {
    if (!!this.props.swapSetting.mnemonic) {
      this.setState((state: IOwnState) => ({
        isOpenPassphrase: !state.isOpenPassphrase,
      }));
    } else {
      this.setState((state: IOwnState) => ({
        isOpenSetting: !state.isOpenSetting,
      }));
    }
  }

  private onDisconnectWallet() {
    this.setState({ wallet: {} as Wallet }, () => {
      this.updateTokenBalance();
    });
    dispatch(clearPassphrase());
    dispatch(setConnectStatus({ isConnected: false }));
  }

  private onModalSettingToggle() {
    this.setState((state: IOwnState) => ({
      isOpenSetting: !state.isOpenSetting,
    }));
  }

  private onModalPassphraseToggle() {
    this.setState((state: IOwnState) => ({
      isOpenPassphrase: !state.isOpenPassphrase,
    }));
  }

  private onModalPassphraseEnter(passphraseCrypto: string) {
    this.initWallet(passphraseCrypto);
    dispatch(setConnectStatus({ isConnected: true }));
  }

  private onSelectDApp(dApp: DApp) {
    this.resetTargetPoolPerDApp(dApp);

    const routeSwaps: IRouteSwap[] = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      dApp
    );
    const priceImpact: IPriceImpact = swapService.getPriceImpact(
      Number(this.state.input.value || 0),
      this.tokenInput,
      this.tokenOutput,
      routeSwaps,
      this.props.bnbPrice
    );

    this.setState((state: IOwnState) => ({
      dApp,
      routeSwaps,
      priceImpact: priceImpact.priceImpact,
      output: {
        ...state.output,
        value: priceImpact.amountOut ? priceImpact.amountOut : '',
      },
    }));
  }

  private onCurrencyInputChange(change: ICurrencyInput) {
    let priceImpact: IPriceImpact = swapService.getPriceImpact(
      Number(change.value || 0),
      this.tokenInput,
      this.tokenOutput,
      this.state.routeSwaps,
      this.props.bnbPrice
    );

    this.setState(
      (state: IOwnState) => ({
        priceImpact: priceImpact.priceImpact,
        input: {
          ...state.input,
          ...change,
        },
        output: {
          ...state.output,
          value: priceImpact.amountOut ? priceImpact.amountOut : '',
        },
      }),
      async () => {
        const address = this.state.input.address;

        if (address !== this.tokenInput.address) {
          await this.initSwapToken();
        }
      }
    );
  }

  private onCurrencyOutputChange(change: ICurrencyInput) {
    this.setState(
      (state: IOwnState) => ({
        output: {
          ...state.output,
          ...change,
        },
      }),
      async () => {
        const address = this.state.output.address;

        if (address !== this.tokenOutput.address) {
          await this.initSwapToken();
        }
      }
    );
  }

  private onFeeSettingChange(value: { isActive: boolean; fee: number }) {
    this.tnxConfig = {
      ...this.tnxConfig,
      fee: value,
    };
  }

  private onGasSlipSettingChange(value: { gasPrice: number; slip: number }) {
    this.tnxConfig = {
      ...this.tnxConfig,
      ...value,
    };
  }

  private onSwapPosition() {
    this.tokenOutput = [
      this.tokenInput,
      (this.tokenInput = this.tokenOutput),
    ][0];
    const routeSwaps: IRouteSwap[] = swapService.getRouteSwap(
      this.tokenInput,
      this.tokenOutput,
      this.state.dApp
    );
    const priceImpact: IPriceImpact = swapService.getPriceImpact(
      Number(this.state.output.value || 0),
      this.tokenInput,
      this.tokenOutput,
      routeSwaps,
      this.props.bnbPrice
    );

    this.setState(
      (state: IOwnState) => ({
        routeSwaps,
        priceImpact: priceImpact.priceImpact,
        input: state.output,
        output: {
          ...state.input,
          value: priceImpact.amountOut ? priceImpact.amountOut : '',
        },
      }),
      () => {
        this.updateTokenBalance();
      }
    );
  }

  private async onEnableToken() {
    const isPrevent: boolean = this.preventEnable();

    if (isPrevent) {
      return;
    }

    this.setState({
      isProcessing: true,
    });

    let status: ITnxReciept = await swapService.approveToken(
      this.state.input.address,
      this.state.wallet,
      this.tnxConfig
    );
    let allowance = BigNumber.from(MAX_UINT256).toString();

    // Approve again with MAX_APPROVE_SAFE (<= BALANCE)
    if (status.errorCode === ERROR_UNPREDICTABLE_GAS_LIMIT) {
      const { balance, decimals } = this.state.input;
      const balanceBN: BigNumber = swapHelper.parseUnitWithDecimals(
        balance,
        decimals
      );

      status = await swapService.approveToken(
        this.state.input.address,
        this.state.wallet,
        this.tnxConfig,
        balanceBN
      );

      allowance = balance;
    }

    this.setState((state: IOwnState) => ({
      isProcessing: false,
      input: {
        ...state.input,
        allowance: status.status === TnxStatus.SUCCESS ? allowance : '0',
      },
    }));

    this.notifyTnxStatus(status, TnxAction.APPROVE);
  }

  private onSafeSwap() {
    const { priceImpact, input } = this.state;
    let confirmSwapMessages: string[] = [];

    if (this.tnxConfig.gasPrice >= SAFE_SWAP.gasPrice) {
      confirmSwapMessages.push(
        `Gas price is high (>= ${SAFE_SWAP.gasPrice}Gwei).`
      );
    }
    if (this.tnxConfig.slip >= SAFE_SWAP.slip) {
      confirmSwapMessages.push(`Slip is high (>= ${SAFE_SWAP.slip}%).`);
    }
    if (
      this.tnxConfig.fee.isActive &&
      this.tnxConfig.fee.fee >= SAFE_SWAP.fee
    ) {
      confirmSwapMessages.push(`Fee is high (>= ${SAFE_SWAP.fee}%).`);
    }
    if (priceImpact >= SAFE_SWAP.impact) {
      confirmSwapMessages.push(
        `Price impact is high (>= ${SAFE_SWAP.impact * 100}%).`
      );
    }
    if (
      [...this.stableCoinAdress, BNB_BEP20.address].includes(input.address) &&
      !!Number(input.balance || 0) &&
      Number(input.value || 0) / Number(input.balance) >= SAFE_SWAP.baseBalance
    ) {
      confirmSwapMessages.push(
        `Input swap is high (>= ${SAFE_SWAP.baseBalance * 100}%).`
      );
    }

    if (confirmSwapMessages.length > 0) {
      this.setState({
        confirmSwapMessages,
        isOpenSwapConfirm: true,
      });
    } else {
      this.swapToken();
    }
  }

  private onSwapConfirmToggle() {
    this.setState((state: IOwnState) => ({
      isOpenSwapConfirm: !state.isOpenSwapConfirm,
    }));
  }

  private onSwapConfirm() {
    this.setState((state: IOwnState) => ({
      isOpenSwapConfirm: !state.isOpenSwapConfirm,
    }));
    this.swapToken();
  }

  private async swapToken() {
    const isPrevent: boolean = this.preventSwap();

    if (isPrevent) {
      return;
    }

    const { wallet, dApp, routeSwaps, input, output } = this.state;

    this.setState({
      isProcessing: true,
    });

    const status: ITnxReciept = await swapService.swapTokens(
      wallet,
      dApp,
      routeSwaps,
      input,
      output,
      this.tnxConfig
    );

    this.notifyTnxStatus(status, TnxAction.SWAP);

    this.setState({ isProcessing: false });

    if (status.status === TnxStatus.SUCCESS) {
      this.setState((state: IOwnState) => ({
        isProcessing: false,
        input: {
          ...state.input,
          value: '',
        },
        output: {
          ...state.output,
          value: '',
        },
      }));

      this.updateTokenBalance();
      this.updateTokenAllowances();
    }
  }

  private notifyTnxStatus(status: ITnxReciept, action: TnxAction) {
    switch (status.status) {
      case TnxStatus.SUCCESS:
        this.toastLink(action, 'success!', status.tnxHash, true);
        break;

      case TnxStatus.FAILED:
        this.toastLink(
          action,
          `failed! ${status.errorCode ? '(' + status.errorCode + ')' : ''}`,
          status.tnxHash
        );
        break;

      case TnxStatus.UNKNOWN:
        this.toastLink(
          action,
          `unknown! ${status.errorCode ? '(' + status.errorCode + ')' : ''}`,
          status.tnxHash
        );
        break;
    }
  }

  private toastLink(
    action: TnxAction,
    messageStatus: string,
    tnxHash?: string,
    isSuccess = false
  ) {
    const { input, output } = this.state;
    const symbol = action === TnxAction.APPROVE ? input.symbol : output.symbol;
    const logoURI =
      action === TnxAction.APPROVE ? input.logoURI : output.logoURI;
    const messageAction: string =
      action === TnxAction.APPROVE ? action : `${action} to`;

    if (isSuccess) {
      toast.success(
        <ToastLink
          message={`${messageAction} ${symbol} ${messageStatus}`}
          link={tnxHash ? `https://bscscan.com/tx/${tnxHash}` : undefined}
          logoURI={logoURI}
        />,
        {
          icon: false,
          autoClose: 60 * 1000,
        }
      );
    } else {
      toast.error(
        <ToastLink
          message={`${messageAction} ${symbol} ${messageStatus}`}
          link={tnxHash ? `https://bscscan.com/tx/${tnxHash}` : undefined}
          logoURI={logoURI}
        />,
        {
          icon: false,
          autoClose: 60 * 1000,
        }
      );
    }
  }

  private checkTokenAllowance(): boolean {
    const { balance, allowance, decimals } = this.state.input;
    const balanceBN: BigNumber = swapHelper.parseUnitWithDecimals(
      balance,
      decimals
    );
    const allowanceBN: BigNumber = swapHelper.parseUnitWithDecimals(
      allowance,
      decimals
    );

    if (balanceBN.lte(allowanceBN)) {
      return true;
    }

    return false;
  }

  private preventEnable(): boolean {
    const { input, isUiLoading, isProcessing } = this.state;
    const isPrevent: boolean =
      isUiLoading || isProcessing || !Number(input.balance || 0);

    return isPrevent;
  }

  private preventSwap(): boolean {
    const { routeSwaps, input, output, isUiLoading, isProcessing } = this.state;
    const isPrevent: boolean =
      isUiLoading ||
      isProcessing ||
      !swapHelper.validRouteSwaps(routeSwaps) ||
      input.address === output.address ||
      !Number(input.value || 0) ||
      Number(input.value || 0) > Number(input.balance || 0) ||
      !Number(output.value || 0);

    return isPrevent;
  }

  private buildPageTitle(): string {
    const { input, output } = this.state;
    const title = `${input.symbol}${
      !input.price ||
      this.stableCoinAdress.includes(input.address) ||
      (!!output.price && !this.baseAddress.includes(output.address))
        ? ''
        : ' (' + helper.roundCurrency(input.price) + ')'
    } → ${output.symbol}${
      !output.price || this.stableCoinAdress.includes(output.address)
        ? ''
        : ' (' + helper.roundCurrency(output.price) + ')'
    }`;

    return title;
  }

  render(): ReactElement {
    const { isConnected } = this.props.swapSetting;
    const {
      dApp,
      routeSwaps,
      priceImpact,
      input,
      output,
      tokenSecurity,
      confirmSwapMessages,
      isOpenSetting,
      isOpenPassphrase,
      isOpenSwapConfirm,
      isTokenInfoLoading,
      isUiLoading,
      isProcessing,
    } = this.state;
    const { address: addressWallet } = this.state.wallet;

    return (
      <Container fluid="lg" className="py-3 py-md-4 py-xl-5">
        <HelmetProvider>
          <Helmet defer={false}>
            <title>{this.buildPageTitle()}</title>
          </Helmet>
        </HelmetProvider>
        <Row>
          <Col
            sm={{
              offset: 2,
              size: 8,
            }}
            md={{
              offset: _.isEmpty(tokenSecurity) ? 3 : 0,
              size: 6,
            }}
            xl={{
              offset: 4,
              size: 4,
            }}
          >
            <div className="fk-container px-3 py-4 p-md-4">
              <div
                className={
                  'd-flex justify-content-between align-items-center ' +
                  style.settingHeader
                }
              >
                <ButtonSelectDApp
                  isDisable={isProcessing || isTokenInfoLoading}
                  dApp={dApp}
                  onChange={this.onSelectDApp}
                />
                <div className="d-flex align-items-center">
                  {isConnected && (
                    <ButtonWalletConnected
                      addressWallet={addressWallet}
                      onClick={this.onDisconnectWallet}
                    />
                  )}
                  <div className="mx-2"></div>
                  <ButtonSetting onClick={this.onModalSettingToggle} />
                  <ModalSetting
                    isOpen={isOpenSetting}
                    onToggle={this.onModalSettingToggle}
                  />
                </div>
              </div>
              <div className="mb-4">
                <CurrencyInput
                  isUiLoading={isUiLoading}
                  isProcessing={isProcessing || isTokenInfoLoading}
                  symbol={input.symbol}
                  address={input.address}
                  logoURI={input.logoURI}
                  balance={input.balance}
                  price={input.price}
                  liquidity={input.liquidity}
                  quoteSymbol={input.quoteSymbol}
                  value={input.value}
                  onChange={this.onCurrencyInputChange}
                />
                <div className="mt-3 text-center">
                  <ButtonSwapPosition
                    isDisabled={isUiLoading || isProcessing}
                    onClick={this.onSwapPosition}
                  />
                </div>
                <CurrencyInput
                  isDisable={true}
                  isTokenInfoLoading={isTokenInfoLoading}
                  isUiLoading={isUiLoading}
                  isProcessing={isProcessing}
                  symbol={output.symbol}
                  address={output.address}
                  logoURI={output.logoURI}
                  balance={output.balance}
                  price={output.price}
                  value={output.value}
                  liquidity={output.liquidity}
                  quoteSymbol={output.quoteSymbol}
                  onChange={this.onCurrencyOutputChange}
                />
              </div>
              <div className="mb-5">
                <div className="mb-3">
                  <PriceImpact
                    isLoading={isUiLoading}
                    priceImpact={priceImpact}
                    tokenInput={_.pick(input, ['address', 'price'])}
                    tokenOutput={_.pick(output, ['address', 'price'])}
                  />
                </div>
                <div className="mb-3">
                  <RouteSwap isLoading={isUiLoading} routeSwaps={routeSwaps} />
                </div>
                <div className="mb-3">
                  <Fee
                    inputFee={input.burn}
                    outputFee={output.burn}
                    onChange={this.onFeeSettingChange}
                  />
                </div>
                <div className="mb-3">
                  <GasSlip onChange={this.onGasSlipSettingChange} />
                </div>
              </div>
              <div className="mb-2">
                {isConnected ? (
                  this.checkTokenAllowance() ? (
                    <ButtonBase
                      className={
                        isProcessing
                          ? 'fk-button-action-progress'
                          : 'fk-button-action'
                      }
                      title={isProcessing ? 'Processing...' : 'Swap'}
                      isDisable={this.preventSwap()}
                      onClick={this.onSafeSwap}
                    />
                  ) : (
                    <ButtonBase
                      className={
                        style.buttonEnable +
                        (isProcessing
                          ? ' fk-button-action-progress'
                          : ' fk-button-action')
                      }
                      title={
                        isProcessing
                          ? 'Processing...'
                          : `Enable ${input.symbol}`
                      }
                      isDisable={this.preventEnable()}
                      onClick={this.onEnableToken}
                    />
                  )
                ) : (
                  <ButtonBase
                    className="fk-button-action"
                    title="Connect Wallet"
                    onClick={this.onConnectWallet}
                  />
                )}
                <ModalPassphrase
                  isOpen={isOpenPassphrase}
                  onToggle={this.onModalPassphraseToggle}
                  onEnter={this.onModalPassphraseEnter}
                />
                <ModalConfirm
                  title="Warning swap!"
                  status={ConfirmStatus.WARNING}
                  contents={confirmSwapMessages}
                  isOpen={isOpenSwapConfirm}
                  onToggle={this.onSwapConfirmToggle}
                  onConfirm={this.onSwapConfirm}
                />
              </div>
            </div>
          </Col>
          {!_.isEmpty(tokenSecurity) && (
            <Col
              sm={{
                offset: 2,
                size: 8,
              }}
              md={{
                offset: 0,
                size: 6,
              }}
              xl={{
                size: 4,
              }}
            >
              <TokenSecurity
                address={tokenSecurity.address}
                pairAddress={tokenSecurity.pairAddress}
                pairName={tokenSecurity.pairName}
              />
            </Col>
          )}
        </Row>
      </Container>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  swapSetting: state.swapSetting,
  tokens: mappingModalSelectToken(state.tokens),
  bnbPrice: state.quoteToken.bnb.price,
});

export default connect(mapState)(withRouter(Swap));
