import { PureComponent, ReactElement } from 'react';
import style from './ButtonSetting.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  onClick: () => void;
}
interface IOwnState {}

class ButtonSetting extends PureComponent<
  IStoreProps & IHOCProps & IOwnProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IStoreProps & IOwnProps & IHOCProps) {
    super(props);

    this.state = {};
    this.onClick = this.onClick.bind(this);
  }

  private onClick() {
    this.props.onClick();
  }

  render(): ReactElement {
    return (
      <button
        className={'p-0 fk-button-icon ' + style.btn}
        onClick={this.onClick}
      >
        <i className='bi bi-gear-fill'></i>
      </button>
    );
  }
}

export default ButtonSetting;
